import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { Message } from 'primeng/primeng';

import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ParamsBean } from 'app/pages/tzb/custom/credit-ccm/common/common-resource/bean/param.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
  selector: 'max-amount-guarantee-contract',
  templateUrl: './max-amount-guarantee-contract.component.html',
  styleUrls: ['./max-amount-guarantee-contract.component.scss']
})

export class MaxAmountGuaranteeContract implements OnInit {

  // 组件查询条件
  paramsBean = new ParamsBean();

  @Input() inputParam: any;

  @Output() outValue = new EventEmitter();

  @Output() closeDialog = new EventEmitter();

  msgs: Message[] = []; // 提示信息
  iscommonBorrower=false;
  guaranteeTypeList = []; // 担保方式
  applyAmt:any; // 申请金额
  flag:boolean = false;//可用金额是否覆盖申请金额标识
  mainGuaranteeTypeName: any; // 授信页担保方式名称
  constructor(public commfunc: Commfunc, private creditCcmService: CreditCcmService) { }

  ngOnInit() {
    this.getCodeValues();
    this.queryList();
    this.guaranteeTypeList = this.inputParam.guaranteeTypeList;  
    if(this.inputParam.guaranteeTypeList && this.inputParam.guaranteeType){
      for(const guaranteeType of this.inputParam.guaranteeTypeList){
        if(this.inputParam.guaranteeType && this.inputParam.guaranteeType == guaranteeType.value){
          this.mainGuaranteeTypeName = guaranteeType.label;
        }
      }
    }  
  }

  // 获取码值
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
  }

  // 查询最高额担保合同列表
  queryList() {
    this.applyAmt = this.inputParam.applyAmt;
    const queryParams = {
      client_no: this.inputParam['custNo'], // 客户号可用 1000789055
      contract_type: '1' // 合同类型固定写最高额
    };
    this.creditCcmService.queryMaxAmountContractList(queryParams).subscribe(data => {
      if (data.returnCode.code === 'success') {
        let array = [];
        if(data.query_result_array){
          for(const result of data.query_result_array){            
            if(result.mature_date && result.mature_date.length==8 && result.mature_date.toString().indexOf("-")==-1 ){
              result.mature_date = result.mature_date.substring(0,4) + "-"+result.mature_date.substring(4,6) +"-" +result.mature_date.substring(6,8);
            }
            array.push(result);
          }
        }
        this.paramsBean.queryList = array;
        if (data.query_result_array.length === 0) {
          this.msgs = [];
          this.msgs = [{ severity: 'info', summary: '提醒', detail: this.inputParam['custNo'] + '没有最高额合同!' }];
          return;
        }
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
      }
    });
  }

  // 选择一条合同信息后查询详细信息并返回
  toConfirm() {
    if (!this.paramsBean.selectedList) {
      this.msgs = [];
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请至少选择一条数据!' }];
      return;
    }
    const gmode = this.paramsBean.selectedList.guarantee_mode;
    this.maxyy();
    // debugger;
    // if(gmode != this.mainGuaranteeTypeName){//与授信页担保方式需一致
    //   this.msgs = [];
    //   this.msgs = [{ severity: 'info', summary: '提示', detail: '请选择主担保方式为' + this.mainGuaranteeTypeName + '类的数据!' }];
    //   return;
    // }
    


  }

maxyy(){
  let transmitParantComponent = [];
    // 通过‘最高额合同编号’查询客户信息
    const queryParam = {
      'contract_no': this.paramsBean.selectedList['contrac_no'],
      'buss_type': "05",
    }
    this.creditCcmService.queryGuaranteeInfo(queryParam).subscribe(data => {
      debugger
      if (data.returnCode.code === 'success') {
        // 拼装客户号、客户名称，形式如下(custNo1,custNo2,custNo3)(custName1,custName2,custName3)
        if (data.query_result_array.length < 1) {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '错误', detail: '通过最高额合同编号查询客户信息为空' }];
          return;
        }
        const tempArray = data.query_result_array;
        for (let i = 0; i < tempArray.length; i++) {
          let transmitParants = {};
          transmitParants['guarantee_mode'] = this.paramsBean.selectedList['guarantee_mode'];
          transmitParants['contrac_amt'] = this.paramsBean.selectedList['contrac_amt'];
          transmitParants['mature_date'] = this.paramsBean.selectedList['mature_date'];
          transmitParants['contrac_no'] = this.paramsBean.selectedList['contrac_no'];
          transmitParants['contrac_balance'] = this.paramsBean.selectedList['contrac_balance'];
          transmitParants['contractMode'] = '03';
          transmitParants['client_no'] = tempArray[i]['client_no'];
          transmitParants['client_name'] = tempArray[i]['client_name'];
          transmitParants['borrower_rltnp'] = tempArray[i]['borrower_rltnp'];

          transmitParants['house_type'] = tempArray[i]['house_type'];//房屋类型
          transmitParants['mortgage_type'] = tempArray[i]['mortgage_type'];//抵押物种类
          transmitParants['guarant_type'] = tempArray[i]['guarant_type'];//担保类型
          transmitParants['contract_max_type'] = this.paramsBean.selectedList['contract_max_type'];
          transmitParants['mortgage_name'] = this.paramsBean.selectedList['mortgage_name'];
          transmitParants['pledge_name'] = this.paramsBean.selectedList['pledge_name'];
          transmitParants['house_type'] = this.paramsBean.selectedList['house_type'];
          transmitParantComponent.push(transmitParants);
        }
        // if(this.flag){//可用金额小于申请金额
          
        // }
        // 传给父组件数据
        this.outValue.emit(transmitParantComponent);
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
      }
    });
}
  // 关闭弹框
  back() {
    this.closeDialog.emit('close');
  }
}