    ngOnInit() {
        let datas = JSON.parse(this.commfunc.getSessionDataCH('groupBranchRelevance'))//客户此页面为客户
        let data = JSON.parse(this.commfunc.getSessionDataCH('groupBranchData'))//客户群
        switch (data.self) {
            case '1': {
                this.state = '0'
            }
                break;
            case '2': {
                this.state = '0'
            }
                break;
            case '3': {
                if (data.selfTwo && data.selfTwo == '1') {
                    this.state = '4'
                } else {
                    this.state = '3'

                }
            }
                break;
            default:
                this.state = '3'
        }
        if(this.state != '4' && this.state != '3'){
            if(datas.canModify == '0'){
                this.state = '1'
            }else{
                this.state = '2'
            }
        }
        // 客户群修改传2，不修改传1
        if(this.state != '4' && this.state != '3'){
            if(datas.canModify == '0'){
                this.state = '1'
            }else{
                this.state = '2'
            }
        }
        if (datas.selfGroup && datas.selfGroup == '1') {
            if(this.state != '4' && this.state != '3'){
                  if(datas.isShow){
                    this.state = '2'
                  }else{
                    this.state = '1'
                  }
            }
            //    客群
            this.getGroupUrl(data)
        } else {
            this.getUrl(datas);
        }

    }
