   // 校验担保人信息完整 added by yuanzhenghe20190417 需求编号 2019000327
    checkGuarantorInfo()
    {
        debugger;
        //判断 是否是 信用卡申请/信用卡续卡/卡要素调整的担保信息页，比如，如果是贷款，则不显示

        if(!this.isCheckGuarantInfoComplete)
        {
            return;
        }

        const params = {
            'custNo': this.guarantorBean.custNo
        };

         // 校验担保人信息完整 added by yuanzhenghe20190417 需求编号 2019000327
         this.creditCcmService.checkGuarantorInfo(params).subscribe(data => {
            //校验担保人信息完整
            if (data.returnCode.code === 'success') {

                if(this.isMock)
                {
                    data.isComplete = 'N';
                    data.birthDate = null;
                }

                if (data.isComplete == 'Y') {
                 
                    this.isIntactGuarantInfo = true;
                    this.isCompleteGuarantInfo  = 'Y';
                    this.isBirthDateEmpty = false;
                } else {
                    this.isIntactGuarantInfo = false;
                    this.isCompleteGuarantInfo  = 'N';
                    if(data.birthDate == null || data.birthDate=='')
                    {
                        this.isBirthDateEmpty = true;
                    }
                }
            } else {
                
                //this.saveGuarantorMsgs = [];
               // this.saveGuarantorMsgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];

              // this.msgs = [];
             //  this.msgs.push({ severity: 'error', summary: '错误', detail: data.returnCode.message });   


            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });     
        });

    }

