    //金额格式处理
    doChangeAmt(mod, i){
        if(mod && mod=='add'){
            if(i && i == 'useAmt'){
                if(this.guarantorBean.useAmt && this.guarantorBean.useAmt.toString().indexOf(",")==-1){
                    this.guarantorBean.useAmt = this.commfunc.formatMoney(this.guarantorBean.useAmt,2);
                }
            } else if(i && i == 'guaranteeContractAmt'){
                if(this.guarantorBean.contractAmt && this.guarantorBean.contractAmt.toString().indexOf(",")==-1){
                    this.guarantorBean.contractAmt = this.commfunc.formatMoney(this.guarantorBean.contractAmt,2);
                }
            }            
        } else {
            if(i && i == 'useAmt'){
                if(this.guarantorBean.useAmt && this.guarantorBean.useAmt.toString().indexOf(",")!=-1){//金额去掉格式
                    this.guarantorBean.useAmt = this.guarantorBean.useAmt.replace(/\,/g, '');
                }
            } else if(i && i == 'guaranteeContractAmt'){
                if(this.guarantorBean.contractAmt && this.guarantorBean.contractAmt.toString().indexOf(",")!=-1){//金额去掉格式
                    this.guarantorBean.contractAmt = this.guarantorBean.contractAmt.replace(/\,/g, '');
                }
            }                   
        }
    }

        /**
     * 判断抵押金额，可用余额，最高额合同金额
     */
    pledgeAmtChange(mod) {
        if(mod && mod == '1'){            
            if (this.guarantorBean.useAmt && this.guarantorBean.contractAmt) {
                let useAmt1 = this.guarantorBean.useAmt;
                if(useAmt1 && useAmt1.toString().indexOf(",")!=-1){
                    useAmt1 = this.guarantorBean.useAmt.replace(/\,/g, '');
                }
                let guaranteeContractAmt1 = this.guarantorBean.contractAmt;
                if(guaranteeContractAmt1 && guaranteeContractAmt1.toString().indexOf(",")!=-1){
                    guaranteeContractAmt1 = this.guarantorBean.contractAmt.replace(/\,/g, '');
                }
                if (Number(useAmt1) < Number(guaranteeContractAmt1)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '担保金额应大于等于最高额合同金额，请重新录入' });
                    this.guarantorBean.useAmt = '';
                    return;
                } else if (Number(useAmt1) < 0) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '担保金额应大于0，请重新录入' });
                    this.guarantorBean.useAmt = '';
                    return;
                }
            }
        } else if(mod && mod == '2'){
            if (this.guarantorBean.useAmt && this.guarantorBean.contractAmt) {
                let useAmt1 = this.guarantorBean.useAmt;
                if(useAmt1 && useAmt1.toString().indexOf(",")!=-1){
                    useAmt1 = this.guarantorBean.useAmt.replace(/\,/g, '');
                }
                let guaranteeContractAmt1 = this.guarantorBean.contractAmt;
                if(guaranteeContractAmt1 && guaranteeContractAmt1.toString().indexOf(",")!=-1){
                    guaranteeContractAmt1 = this.guarantorBean.contractAmt.replace(/\,/g, '');
                }
                if (Number(useAmt1) < Number(guaranteeContractAmt1)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '最高额合同金额应小于担保金额，请重新录入' });
                    this.guarantorBean.contractAmt = '';
                    return;
                } else if (Number(guaranteeContractAmt1) < 0) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '最高额合同金额应大于0，请重新录入' });
                    this.guarantorBean.contractAmt = '';
                    return;
                }
            }
        }
    }    
    
