import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';

import { Message, SelectItem } from 'primeng/primeng';

import { ResourceBean } from '../bean/resource.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
    selector: 'app-resource-add',
    templateUrl: './resource-add.component.html',
    styleUrls: ['./resource-add.component.scss'],
})
// tslint:disable-next-line:component-class-suffix
export class ResourceAdd implements OnInit {
    @Input() inputParams;
    @Output() closeDialog = new EventEmitter();

    // 资源ean
    resourceBean: ResourceBean = new ResourceBean();
    // 提示信息
    msgs: Message[] = [];
    successMsgs: Message[] = [];
    basicSourceEnNames: any = [];
    myformChild: FormGroup = this.fb.group({
        'basicSourceName': new FormControl('', [Validators.pattern(/^[a-zA-Z0-9\u4e00-\u9fa5]{1,30}$/), Validators.required]),
        'basicSourceEnName': new FormControl('', [Validators.pattern(/^[a-zA-Z0-9\u4e00-\u9fa5]{1,30}$/), Validators.required]),
        'basicDescription': new FormControl('', [Validators.required]),
    }); //校验
    constructor(
        public fb: FormBuilder,
        public commfunc: Commfunc,
        private creditCcmService: CreditCcmService,
    ) { }
    ngOnInit() {
        this.getCodeValues();
    }

    // 数据字典
    getCodeValues() {
        this.commfunc.codeValue();
        const codeValues = this.commfunc.codeDatas;
        this.basicSourceEnNames = codeValues['basicSourceEnNames'];
        // 获取用户信息
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.resourceBean.tellerNo = commonHeader['userId'];
        this.resourceBean.tellerOrgNo = commonHeader['orgId'];
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.resourceBean.tellerName = chName['userName'];
        this.resourceBean.tellerOrgName = chName['orgName'];

    }
    preValidateForSubmit() {
        for (const i in this.myformChild.controls) this.myformChild.controls[i].markAsDirty();
        for (const i in this.myformChild.controls) {
            if (!this.myformChild.controls[i].valid) {
                return false;
            }
        }
        return true;
    }
    addResource() {
        if (!this.preValidateForSubmit()) return;
        this.creditCcmService.addResource(this.resourceBean).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.successMsgs = [{ severity: 'success', summary: '提示：', detail: '资源保存成功' }];
            } else {
                this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
            }
        });
    }
    toClose() {
        this.closeDialog.emit();
    }
}
