import { Component, OnInit } from '@angular/core';

import { Message } from 'primeng/primeng';

import { ParamsBean } from '../bean/params.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { ParentChildCommunicationService } from 'app/pages/tzb/custom/credit-ccm/common/service/parent-child-comm.service';

@Component({
    selector: 'app-credit-authorization',
    templateUrl: './credit-authorization.component.html',
    styleUrls: ['./credit-authorization.component.scss']
})

// tslint:disable-next-line:component-class-suffix
export class CreditAuthorization implements OnInit {
    // 构造器
    constructor(private creditCcmService: CreditCcmService,
        private communicationService: ParentChildCommunicationService) { }

    // 提示信息
    msgs: Message[] = [];

    // 初始加载
    ngOnInit() {
        this.queryCreditAuthList();
    }

    // 查询征信授权信息列表
    queryCreditAuthList() {
        this.creditCcmService.queryCreditAuthList('').subscribe(data => {
            //////console.log(data);
            if (data.Service.Header.RetStatus === 'S') {

            }
        });
    }

    // 分页参数
    paramsBean = new ParamsBean();

    // 发送短信
    sendSms() {
        const queryBean = {
            'MESSAGE_CODE': 'C201',
            'TRAN_DATE': '20180615154623',
            'TRAN_TIMESTAMP': '20180615154623',
            'CONSUMER_SEQ_NO': '1111111111',
            'BANK_SYSTEM_TYPE': 'TZYH',
            'CHANNEL_TYPE': '111',
            'BRANCH_ID': '0000',
            'MESSAGE_ARRAY': [{
                'MESSAGE_TEMPLATE_CODE': '32697',
                'CLIENT_NO': '7949546',
                'SEND_TIME': '164649896',
                'PHONE_NO': '13945677895       ',
                'ACCT_NO': '11111111',
                'PRIORITY_LEVEL': 'C201',
                'TEMPLATE_PARA_ARRAY': [{
                    'PARA_NAME': 'C201',
                    'FIELD_TYPE': 'C201',
                    'PARA_VALUE': 'C201'
                }
                ]
            }]
        }
        this.creditCcmService.sendSms(queryBean).subscribe(data => {
            //////console.log(data);
            if (data.Service.Header.RetStatus === 'S') {
                this.msgs = [];
                this.msgs.push({ severity: 'info', summary: '提示', detail: '短信发送成功！' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '错误', detail: data.Service.Header.RetMsg });
            }
        });
    }

    // 查看征信报告
    viewCreditReport() {

    }

    // 征信查询
    queryCreditInfo() {
        const queryBean = {
            'CLIENT_NAME': '一片红',
            'GLOBAL_ID': '20180615154623'
        }
        this.creditCcmService.queryCreditInfo(queryBean).subscribe(data => {
            //////console.log(data);
            if (data.Service.Header.RetStatus === 'S') {
                this.msgs = [];
                this.msgs.push({ severity: 'info', summary: '提示', detail: '征信查询成功！' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '错误', detail: data.Service.Header.RetMsg });
            }
        });
    }

    // 新增征信授权
    addCreditAuth() {

    }

    // 删除征信授权
    delCreditAuth() {

    }
}