import { Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';
import { Component, OnInit, Input, OnChanges, Output, EventEmitter } from '@angular/core';

import { Message, ConfirmationService } from 'primeng/primeng';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

import { ParamsBean } from './bean/param.bean';
import { ResourceBean } from './bean/resource.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'app-resource',
    templateUrl: './common-resource.component.html',
    styleUrls: ['./common-resource.component.scss'],
})
// tslint:disable-next-line:component-class-suffix
export class CommonResource implements OnInit, OnChanges {
    @Input() bookmark_data: any;
    @Output() closeDialog = new EventEmitter();
    // 查询条件
    paramsBean: ParamsBean = new ParamsBean();
    // 资源bean
    resourceBean: ResourceBean = new ResourceBean();

    // 新增页面
    displayAdd = false;
    // 资源英文名
    basicSourceEnName: string;
    // 展示资源页面
    displayResourcePage = false;
    // 页签调用页面的时候展示
    displayBookmark = false;
    // 控制新增删除按钮展示
    displayButton = true;
    // 预览
    displayView = true;
    // 提示信息
    msgs: Message[] = [];
    bookmarkId = ''; // 页签编号
    resourceList: any = []; // 资源编号list
    constructor(
        private router: Router,
        private creditCcmService: CreditCcmService,
        private confirmationService: ConfirmationService,
        public commfunc: Commfunc,
    ) { }
    codeValues = [];
    ngOnInit() {
        this.query('queryBtn');
        this.commfunc.codeValue();
        this.codeValues = this.commfunc.codeDatas['basicSourceEnNames'];
    }
    ngOnChanges() {
        this.bookmarkId = this.bookmark_data.bookmarkId;
        if (this.bookmark_data.bookmark === 'bookmark') {
            this.displayButton = false;
            this.displayBookmark = true;
        }
    }
    query(param?: string) {
        this.displayAdd = false;
        if (param === 'queryBtn') {
            this.paramsBean.startIndex = 0;
            this.paramsBean.pageNum = 1;
        }
        const queryBean = {
            'basicSourceEnName': this.resourceBean.basicSourceEnName,
            'basicSourceName': this.resourceBean.basicSourceName,
            'viewSize': this.paramsBean.pageSize,
            'viewIndex': this.paramsBean.pageNum
        };
        this.creditCcmService.querySources(queryBean).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.paramsBean.queryList = data.list;
                this.paramsBean.totalRecords = data.totalSize;
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
                return;
            }
        });
    }
    // 预览查看
    viewResource() {
        
        if (!this.paramsBean.selectedList || this.paramsBean.selectedList.length > 1) {
            this.msgs = [{ severity: 'info', summary: '提示:', detail: '只能选择一条数据!' }];
            return;
        }
        this.displayResourcePage = true;
        this.basicSourceEnName = this.paramsBean.selectedList[0].basicSourceEnName;
    }
    // 分页按钮事件
    paginate(event) {
        this.paramsBean.pageNum = event.page + 1;
        this.paramsBean.pageSize = event.rows;
        this.paramsBean.startIndex = event.first;
        this.query();
    }
    add() {
        this.displayAdd = true;
    }
    // 页签关联资源保存
    toConfirm() {
        this.resourceList = [];
        if (this.paramsBean.selectedList.length > 0) {
            this.resourceList = this.paramsBean.selectedList;
            this.closeDialog.emit(this.paramsBean.selectedList);
        } else {
            this.msgs = [{ severity: 'info', summary: '提示!', detail: '至少选择一条数据!' }];
            return;
        }
    }

    // 资源删除
    delete() {
        if (!this.paramsBean.selectedList) {
            this.msgs = [{ severity: 'info', summary: '提示!', detail: '至少选择一条数据!' }];
            return;
        }
        this.confirmationService.confirm({
            message: '确定要删除这些数据吗？',
            accept: () => {
                // 多个资源编号
                const resourceIds = [];
                for (const parmas of this.paramsBean.selectedList) {
                    resourceIds.push(parmas.basicSourceId);
                }
                const resourceIdJson = {
                    'basicSourceIds': resourceIds
                };
                // 调用组件删除接口
                this.creditCcmService.deleteResource(resourceIdJson).subscribe(data => {
                    if (data.returnCode.code === 'success') {
                        this.query();
                    } else {
                        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
                    }
                });
            },
            reject: () => {
                this.msgs = [{ severity: 'info', summary: '删除', detail: '你选择了拒绝删除！' }];
            }
        });
    }

    // 重置
    reset() {
        this.paramsBean = new ParamsBean();
        this.resourceBean = new ResourceBean();
    }
}
