import { Component, OnInit, Input, ViewChild, ViewContainerRef, ComponentFactoryResolver } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Message } from 'primeng/primeng';


import { BookmarkBean } from '../bean/bookmark.bean';
import { ParamsBean } from '../bean/params.bean';
import { ResourceItem } from 'app/pages/tzb/custom/credit-ccm/credit-container-resource/resource-item';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { ContainerResourceService } from 'app/pages/tzb/custom/credit-ccm/credit-container-resource/container-resource.service';
import { ResourceTemplateComponent } from 'app/pages/tzb/custom/credit-ccm/credit-container-resource/resource-template';
@Component({
    selector: 'app-bookmark-view',
    templateUrl: './common-bookmark-view.component.html',
    styleUrls: ['./common-bookmark-view.component.scss'],
})
// tslint:disable-next-line:component-class-suffix
export class BookmarkView implements OnInit {
    @Input() detailData;
    msgs: Message[] = [];
    bookmarkBean: BookmarkBean = new BookmarkBean();
    paramsBean: ParamsBean = new ParamsBean();
    basicSourceList: any = [];
    componets: any = [];
    @ViewChild('childContainer', { read: ViewContainerRef }) childContainer: ViewContainerRef;
    resource: ResourceItem;
    constructor(
        private router: Router,
        private routes: ActivatedRoute,
        private viewContainer: ViewContainerRef,
        private componentFactoryResolver: ComponentFactoryResolver,
        private creditCcmService: CreditCcmService,
        private containerResourceService: ContainerResourceService) { }
    ngOnInit() {
        this.bookmarkBean = this.detailData;
        this.queryBookmarkList();
    }
    queryBookmarkList() {
        const qureyBean = {
            'bookmarkId': this.bookmarkBean.bookmarkId,
            'bookmarkName': this.bookmarkBean.bookmarkName
        };
        this.creditCcmService.queryBookmarkResourceList(qureyBean).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.basicSourceList = data.list;
                this.loadChild(this.basicSourceList);
            } else {
                this.msgs = [{ severity: 'error', summary: '查询失败', detail: data.returnCode.message }];
            }
        });
    }

    loadChild(basicSources) {
        const tempData = {
            'isPreview': true,
        };
        const params = {
            'transferCreditData': tempData,
            'bookmarkId': this.bookmarkBean.bookmarkId,
        };
        for (const basicSource of basicSources) {
            this.resource = this.containerResourceService.getResources(basicSource['basicSourceEnName'], params);
            const componentFactory = this.componentFactoryResolver.resolveComponentFactory(this.resource.component);
            const componentRef = this.childContainer.createComponent(componentFactory);
            (<ResourceTemplateComponent>componentRef.instance).data = params;
        }
    }

}
