import { Directive , Input , ElementRef} from "@angular/core";
import { NG_VALIDATORS , NgModel , FormControl , Validator , AbstractControl , ValidatorFn } from '@angular/forms';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Directive({
    selector : '[inputNumber][ngModel]' ,
    providers : [
        { provide : NG_VALIDATORS , useExisting : InputNumberDirective ,multi : true}
    ]
})

export class InputNumberDirective implements Validator {
    @Input() inputNumberReg : string;

    constructor (private el :ElementRef,public commfunc: Commfunc ) {}

    validate (control : FormControl) : { [key : string] : any ;} {
        if (!control.value || !this.inputNumberReg) return null;
        let inputNumberValue = this.el.nativeElement.value;
        const DECIMAL_REGEXP = new RegExp(this.inputNumberReg);
        const checkResult = DECIMAL_REGEXP.test(inputNumberValue.replace(/,/g,''));//校验前去千分位
        if (!checkResult) {
            inputNumberValue = null;
        }    
        this.el.nativeElement.value = inputNumberValue;
        return checkResult ? null : { 'inputNumber' : { value : control.value } };
    }
}