import { Component, OnInit } from '@angular/core';
import { ConfirmationService, Message } from 'primeng/primeng';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { AppproResultBean, SubApproveMapBean, BussinessMap } from './bean/wait-apply.bean';
import { CustomeHttpService } from '../../../../../@uisftech/view/http/uisftech-view.http.service';
import { Router, ActivatedRoute } from '@angular/router';
import { CreditCcmService } from '../common/http/credit-ccm.http.service';

@Component({
    selector: 'wait-apply',
    templateUrl: './wait-apply.component.html',
    styleUrls: ['./wait-apply.component.scss'],
    providers: [Commfunc, CustomeHttpService,CreditCcmService]
})

export class WaitApply implements OnInit {
    msgs: Message[] = [];
    appproResultBean: AppproResultBean = new AppproResultBean();
    subApproveMapBean: SubApproveMapBean = new SubApproveMapBean();
    bussinessMap: BussinessMap = new BussinessMap();
    userId: string;
    ex: any = [];
    dataMap: any = {};
    code: any;
    idenTypes: any = [];
    // littleEvent:any=[]
    juege: boolean = false;//默认是可以点的
    taskId:string;
    bigClassName:string;
    littleClassName:string;
    littleClassNo:string;
    isCreditCardElement: boolean = false;
    flag: string;
    isHistory: boolean = false;
    cardDisplay: boolean = true;

    khcx: boolean = false;
    xykjh: boolean = false;
    defqsq: boolean = false;

    constructor(
        public commfunc: Commfunc,
        public httpService: CustomeHttpService,
        public router: Router,
        public activatedRoute: ActivatedRoute,
        public creditCcmService:CreditCcmService,
    ) {
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.bigClassName=this.activatedRoute.snapshot.params['bigClassName'];
        this.littleClassName = this.activatedRoute.snapshot.params['littleClassName'];
        // 区分卡是否是卡要素调整
        this.littleClassNo = this.activatedRoute.snapshot.params['littleClassNo'];

        if (this.littleClassNo == 'YLCCCL_KYSTZ') {
            this.cardDisplay = true;
            this.isCreditCardElement = true;
        } else if (this.littleClassNo == 'RUM0002') {
            this.cardDisplay = true;
            this.isCreditCardElement = false;
        } else if (this.littleClassNo == 'YLCCCL_KHCX'){
            this.cardDisplay = false;
            this.khcx = true;
        } else if (this.littleClassNo == 'YLCCCL_XYKJH'){
            this.cardDisplay = false;
            this.xykjh = true;
        } else if (this.littleClassNo == 'YLCCCL_DEFQSQ') {
            this.cardDisplay = false;
            this.defqsq = true;
        }

        // this.cardDisplay = false;
        // if (this.activatedRoute.snapshot.params['dataMap']) {
        //     this.dataMap = JSON.parse(this.activatedRoute.snapshot.params['dataMap']);
        // }

        // 历史 和 待办 判断-隐藏按钮
        this.flag = this.activatedRoute.snapshot.params['markFlag'];
        if (this.flag == '1') {
            this.isHistory = false;
        } else {
            this.isHistory = true;
        }

        if (this.commfunc.getSessionDataCH('waitApplyParams')) {
            this.dataMap = JSON.parse(this.commfunc.getSessionDataCH('waitApplyParams'));
        }
        
       this.taskId=this.activatedRoute.snapshot.params['taskId'];
    }
    ngOnInit() {
        this.codeValues() //调用方法，获取全部码值
        this.idenTypes = this.code['ALL_IDEN_TYPE'];//证件类型
        // this.littleEvent = this.code['ScheduleType'];//事项类型
    }
    //同意
    doSure(type) {
        this.juege = true;
        let param = {
            custNo: this.dataMap.custNo,
            custName: this.dataMap.custName,
            documentType: this.dataMap.documentType,//证件类型
            documentNo: this.dataMap.documentNo,//证件号码
            creditBarCode: this.dataMap.creditBarCode,//条形码
            unionpayStatus: this.dataMap.unionpayStatus,//银联错误信息（错误码|错误信息）
            taskId:this.taskId,
            creditResultId:this.dataMap.creditResultId,//授信最终结果信息表主键
            custRequestNo:this.dataMap.custRequestNo,//客户请求编号
            approve:type,// approve审批状态 同意0否决1 默认同意
        }
        this.creditCcmService.distributCardNextTask(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.doUp();
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.juege = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.juege = false;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })

    }
    //否决
    veto() {
        this.juege = true;
        this.subApproveMapBean.result = '1';
        this.bussinessMap.approve = '1';
        this.vetoQuery();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    vetoQuery() {
        this.appproResultBean.bussinessMap = this.bussinessMap;
        this.appproResultBean.userId = this.userId;
        this.appproResultBean.approveMap = this.subApproveMapBean;
        this.appproResultBean.taskId = this.taskId;
        this.httpService.bpmNextTask(this.appproResultBean).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.doUp();
                // window.history.go(-1);
            } else {
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.juege = false;
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.ex = [];
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
                delete s.extParam.specifyNextOpers
                let commonHeader = s;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            this.juege = false;
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    // 否决 - 大额分期
    defqveto() {
        this.juege = true;
        let param = {
            custRequestNo: this.dataMap.custRequestNo, // 客户请求编号
            unionpayStatus: this.dataMap.unionpayStatus, // 银联状态
            taskId: this.taskId, // 任务流程编号
        }
        this.httpService.unionpayMistakeVeto(param).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                this.doUp();
            } else {
                this.juege = false;
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.juege = false;
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    // 同意 - 大额分期(单批放款)
    defqAgree() {
        this.juege = true;
        let param = {
            cardNo: this.dataMap.cardNo, // 卡号
            custRequestNo: this.dataMap.custRequestNo, // 客户请求编号
            unionpayStatus: this.dataMap.unionpayStatus, // 银联状态
            creditBarCode: this.dataMap.creditBarCode, // 条形码
            custName: this.dataMap.custName, // 客户名称
            custNo: this.dataMap.custNo, // 客户号
            documentNo: this.dataMap.documentNo, // 证件号码
            documentType: this.dataMap.documentType, // 证件类型
            deadLine: this.dataMap.deadLine, // 到期日
            tellerNo: this.dataMap.tellerNo, // 经办人
            tellerOrgNo: this.dataMap.tellerOrgNo, // 经办人机构
            taskId:this.taskId, // 任务流程编号
        }
        this.httpService.singleIssueCard(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.doUp();
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.juege = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.juege = false;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })

    }

    doUp() {
        setTimeout(() => {
            // this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/worktask'], { queryParams: { flag: '1', 'id': '2', 'jump': '1' } });
            window.history.go(-1);
        }, 500)
    }
}
