import { Location } from '@angular/common';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Component, OnInit, Input, Output, EventEmitter, AfterViewInit, ViewChild } from '@angular/core';

import { Message } from 'primeng/primeng';

import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { checkCard } from 'app/@uisftech/common/service/validator';
import { CreditBorrowerBean } from '../../bean/credit-borrower.bean';
import { CreditDebt } from 'app/pages/tzb/custom/credit-ccm/credit-debt/credit-debt.component';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { CreditDebtBean } from '../../credit-guarantee/bean/guarantor.bean';

@Component({
    selector: 'credit-borrower-detail',
    templateUrl: './credit-borrower-detail.component.html',
    styleUrls: ['./credit-borrower-detail.component.scss'],
    providers: [CreditCcmService]
})

// 共同借款人详情
// tslint:disable-next-line:component-class-suffix
export class CreditBorrowerDetail implements OnInit {
    debtValue: { transferCreditData: { custName: any; custNo: any; flag: string; orgCode: any; }; };
    creditDebtFlag: boolean=false;
    code: any;
    // 获取父组件传值：facProcessQuestionId
    @Input() param_data: any;

    // 与共同借款人关系
    borrowerRelationshipList = [];

    // 提示信息
    msgs: Message[] = [];

    // 保存成功提示框
    successMsgs: Message[] = [];

    // 问题主键
    cqiId = '';

    // 共同借款人bean
    creditBorrowerBean: CreditBorrowerBean = new CreditBorrowerBean();
    creditDebtBean: CreditDebtBean = new CreditDebtBean();

    @ViewChild(CreditDebt)
    private creditDebt: CreditDebt;

    // 构造器
    constructor(
        public fb: FormBuilder,
        public commfunc: Commfunc,
        private creditCcmService: CreditCcmService,
        private location: Location
    ) {
        this.getCodeValues();
        // 贷款限额
        this.borrowerRelationshipList = this.code['borrowerRelationship'];

    }

    // 初始化信息
    ngOnInit() {
        debugger
        this.creditBorrowerBean.custName = this.param_data.custName;
        this.queryFacQuestions(this.param_data);
        this.cqiId = this.param_data.detailBorrower.cqiId;
        

    }

    // 数据字典
    getCodeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量

    }

    // 根据客户号查询客户信息
    queryCusts() {
        const params = {
            'custNo': this.creditBorrowerBean.commonBorrowerCustNo
            // custNo:'5100029196'
        };
        // 调用查询客户中心接口，查询客户相关信息 
        this.creditCcmService.queryCustNeedInfo(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.creditBorrowerBean.commonBorrowerCustNo = data.custNo;
                this.creditBorrowerBean.commonBorrowerCustName = data.custName;
                this.creditBorrowerBean.commonBorrowerCardType = data.idenType
                this.creditBorrowerBean.commonBorrowerCardNO = data.idenNum;
                this.creditBorrowerBean.commonBorrowerTelephone = data.telNum;
                
            } else {
                this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
            }
        });
    }

    // 查询共同借款人单条信息
    queryFacQuestions(facQuestions) {
        const params = {
            'custRequestNo': this.param_data['custRequestNo'],
            'questionType': '01',
            'cqiId': facQuestions.detailBorrower.cqiId
        };
        this.creditCcmService.queryFacQuestions(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                for (const item of data.list) {
                    if(item.cqiId==facQuestions.detailBorrower.cqiId){
                        const questionReslponse = JSON.parse(item['questionReslponse']);
                        questionReslponse.cqiId = item['cqiId'];
                        this.creditBorrowerBean = questionReslponse;
                        setTimeout(() => {
                            this.queryEsbDebtInfo(this.creditBorrowerBean.commonBorrowerCustNo,this.creditBorrowerBean.commonBorrowerCustName)
                        }, 100);
                    }
                    
                }
            } else {
                this.msgs = [{ severity: 'error', summary: '错误:', detail: data.returnCode.message }];
            }
        });
    }


    // 债务信息查询
    queryEsbDebtInfo(cust,custName) {
        let session = window.sessionStorage;
        let orgId = JSON.parse(this.commfunc.getSessionData('commonHeader')).orgId;
        let param = {
            custName: custName,
            custNo: cust,
            flag: '1',
            orgCode:orgId
        }
        this.debtValue={
            transferCreditData:param,
         }
    }
}