// tslint:disable-next-line:quotemark
import { Directive } from "@angular/core";
import { NG_VALIDATORS, NgModel, FormControl } from '@angular/forms';

export function positiveNumber(c: FormControl) {
    const NUM_REGEXP = /^\d+(\.\d{1,2})?$/;
    if (!c.value) {
        return true;
    } else {
        const num = c.value.replace(/,/g, '');
        return NUM_REGEXP.test(num) ? null : {
            positiveNumber: { value: false }
        };
    }
}

@Directive({
    // tslint:disable-next-line:directive-selector
    selector: '[positiveNumber][ngModel]',
    providers: [
        { provide: NG_VALIDATORS, useValue: positiveNumber, multi: true }
    ]
})

// tslint:disable-next-line:directive-class-suffix
export class PositiveNumber { }