	// 查询
	query(params?: string) {
		if (params === 'queryBtn') {
			this.pageBean.startIndex = 0;
			this.pageBean.currentPage = 1;
			this.pageBean.pageSize = 10;
		}
		const queryParams = {
			'custNo': this.viewRelationBean.custNo,
			'custName': this.viewRelationBean.custName,
			'productName': this.viewRelationBean.productName,
			'loanAcctNo': this.viewRelationBean.loanAcctNo,
			'creditType': this.viewRelationBean.creditType,
			'unionpayStatus': this.viewRelationBean.unionpayStatus,
			'approvalStatus': this.viewRelationBean.approvalStatus,
			'viewIndex': this.pageBean.currentPage,
			'viewSize': this.pageBean.pageSize,
			'tellerNo': this.viewRelationBean.tellerNo,
			'tellerOrgNo': this.viewRelationBean.tellerOrgNo,
		};
		// 日期处理,如果日期为空,则不传给后台
		if (this.startDate != undefined && this.startDate != null) {
			queryParams['tellerDateStart'] = this.commfunc.transDateN(this.startDate);
		}
		if (this.endDate != undefined && this.endDate != null) {
			queryParams['tellerDateEnd'] = this.commfunc.transDateN(this.endDate);
		}
		this.creditCcmService.queryCreditCardUpdateItems(queryParams).subscribe(data => {
			if (data.returnCode.code === 'success') {
				this.pageBean.queryList = data.list;
				this.pageBean.totalRecords = data.totalSize;
				this.isCommitDis = true;
			} else {
				this.isCommitDis = true;
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.isCommitDis = true;
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
		})
	}

	// 分页
	paginate(event) {
		this.pageBean.pageSize = event.rows * 1;
		let currentPage = event.page + 1;
		this.pageBean.currentPage = currentPage;
		this.pageBean.startIndex = (currentPage - 1) * this.pageBean.pageSize;
		this.query();
	}

	// 查询客户信息
	lookReport(col) {
		this.certType = '';
		this.certID = '';
		let param = {
			custNo: col.custNo
		}
		let ob = this.httpServices.selectCustBriefInfo(param);
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.certType = data.idenType;
				this.certID = data.idenNum;
				if (this.certType == null && this.certType == '' && this.certType == undefined) {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: '客户证件类型为空' });
					return
				}
				if (this.certID == null && this.certID == '' && this.certID == undefined) {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: '客户证件号为空' });
					return
				}
				this.queryUserReport(col);
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
		})
	}

	// 查询客户是否存在BI报告
	queryUserReport(col) {
		let param = { custNo: col.custNo }
		let params = this.commfunc.handleParams(param)
		let TZB_HTTP_CUS1 = this.TZB_HTTP_CUS1 + API.creditReportCheck;
		let _self = this;
		$.ajax({
			url: TZB_HTTP_CUS1,
			type: 'POST',
			data: JSON.stringify(params),
			async: false,
			headers: {
				'x-ajax': '1',
				'Content-Type': 'application/json'
			},
			dataType: 'json',
			success: function (data) {
				if (data.returnCode.code == 'success') {
					if (data.isCreate == "1") {
						let userId = JSON.parse(_self.commfunc.getSessionData('commonHeader')).userId;
						let userName = JSON.parse(_self.commfunc.getSessionDataCH('chName')).userName;
						_self.creatMainMsg({ custNo: col.custNo, modNum: data.updatedNum, custName: col.custName, qryUserId: userId, cretType: _self.certType, cretNo: _self.certID, qryUserName: userName, crdtReportNo: data.crdtReportNo, accessWay: '1' })
					} else {
						_self.inValueReportId = data.reportId;
						_self.reportCustNm = col.custNo;
						_self.reportDisplay = true;
					}
				} else {
					_self.msgs = [];
					_self.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
				}
			},
			error: function (err) {
			}
		})
	}

	// 生成BI报告
	creatMainMsg(param) {
		this.progress = true;
		let params = this.commfunc.handleParams(param);
		this.httpServices.createReportMainInfo(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.inValueReportId = data.reportId;
				this.reportCustNm = param.custNo;
				this.progress = false;
				this.reportDisplay = true;
			} else {
				this.progress = false;
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
			}
		}, err => {
			this.progress = false;
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
		})
	}

