import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { PageBean } from './bean/page.bean';
import { ViewConfigBean } from './bean/view-config.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router } from '@angular/router';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-common-view',
  templateUrl: './common-view.component.html',
  styleUrls: ['./common-view.component.scss'],
})
export class CommonViewComponent implements OnInit {
  @Input() viewRelation_data: any;
  @Output() closeDialog = new EventEmitter();
  pageBean = new PageBean();
  viewConfigBean = new ViewConfigBean();
  msgs: Message[] = [];
  sendViewPageData: any;
  viewPageName: string;
  showConfirmBtn = false;
  showViewPage = false;

  constructor(
    public commfunc: Commfunc,
    private router: Router,
    private creditCcmService: CreditCcmService,
    private confirmationService: ConfirmationService
  ) { }

  ngOnInit() {
    // 获取用户信息
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.viewConfigBean.tellerNo = commonHeader['userId'];
    this.viewConfigBean.tellerOrgNo = commonHeader['orgId'];
    let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.viewConfigBean.tellerName = chName['userName'];
    this.viewConfigBean.tellerOrgName = chName['orgName'];
    // 判断是否是“页面关联”引入页面的入口
    if (this.viewRelation_data && this.viewRelation_data.flag === 'viewrelationAdd') {
      this.showConfirmBtn = true;
    }
    this.query('queryBtn');
  }
  // 重置
  resetBtn() {
    this.viewConfigBean = new ViewConfigBean();
  }
  // 查询
  query(params?: string) {
    if (params === 'queryBtn') {
      this.pageBean.startIndex = 0;
      this.pageBean.currentPage = 1;
    }
    const queryParams = {
      'viewConfigId': this.viewConfigBean.viewConfigId,
      'viewName': this.viewConfigBean.viewName,
      'viewIndex': this.pageBean.currentPage,
      'viewSize': this.pageBean.pageSize,
    };
    // 调用接口保存
    this.creditCcmService.queryViewListByLike(queryParams).subscribe(data => {
      if (data.returnCode.code === 'success') {
        this.pageBean.queryList = data.list;
        this.pageBean.totalRecords = data.totalSize;
      } else {
        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
        return;
      }
    });
  }
  // 预览
  preview() {
    if (!this.pageBean.selectedList || this.pageBean.selectedList.length !== 1) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请选择一条数据！' }];
      return;
    }
    this.viewPageName = this.pageBean.selectedList[0]['viewName'];
    this.sendViewPageData = {
      'viewConfigId': this.pageBean.selectedList[0]['viewConfigId'],
      'viewName': this.pageBean.selectedList[0]['viewName'],
      'isPreview': true,
    }
    this.showViewPage = true;
  }
  // 添加
  add() {
    this.router.navigate(['/pages/tzb/custom/credit-ccm/viewAdd']);
  }
  // 修改
  update() {
    if (!this.pageBean.selectedList || this.pageBean.selectedList.length !== 1) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请选择一条数据修改！' }];
    } else {
      const param = {
        'viewConfigId': this.pageBean.selectedList[0]['viewConfigId'],
        'viewName': this.pageBean.selectedList[0]['viewName'],
        'updateFlag': true
      };
      this.router.navigate(['/pages/tzb/custom/credit-ccm/viewUpdate', JSON.stringify(param)]);
    }
  }
  // 删除
  delete() {
    if (!this.pageBean.selectedList || this.pageBean.selectedList.length < 1) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请至少选择一条数据！' }];
      return;
    }
    this.confirmationService.confirm({
      message: '确定要删除这些数据吗？',
      accept: () => {
        let temp = '';
        this.pageBean.selectedList.forEach(element => {
          temp += element.viewConfigId + ',';
        });
        temp = temp.substr(0, temp.length - 1);
        const param = {
          'viewConfigId': temp,
          'tellerOrgNo': this.viewConfigBean.tellerNo,
          'tellerOrgName': this.viewConfigBean.tellerOrgName,
          'tellerNo': this.viewConfigBean.tellerNo,
          'tellerName': this.viewConfigBean.tellerName,
        };
        this.creditCcmService.deteleViewConfig(param).subscribe(data => {
          if (data.returnCode.code === 'success') {
            this.query();
          } else {
            this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
          }
        });
      },
      reject: () => {
        this.msgs = [{ severity: 'info', summary: '删除', detail: '你选择了拒绝删除！' }];
      }
    });
  }
  // 确定
  confirm() {
    if (!this.pageBean.selectedList || this.pageBean.selectedList.length > 1) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请选择一条数据！' }];
      return;
    } else {
      this.closeDialog.emit(this.pageBean.selectedList);
    }

  }
  // 分页按钮事件
  paginate(event) {
    this.pageBean.currentPage = event.page + 1;
    this.pageBean.pageSize = event.rows;
    this.pageBean.startIndex = event.first;
    this.query();
  }
}
