import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Message, ConfirmationService } from 'primeng/primeng';

import { PageBean } from '../common-view/bean/page.bean';
import { ViewRelationBean } from './bean/viewrelation.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-common-viewrelation',
  templateUrl: './common-viewrelation.component.html',
  styleUrls: ['./common-viewrelation.component.scss'],
})
export class CommonViewrelationComponent implements OnInit {
  pageBean = new PageBean();
  viewRelationBean = new ViewRelationBean();
  msgs: Message[] = [];
  businessTypeList = []; //业务类型数据字典
  selectProductDisplay: boolean = false;//弹出框
  isCatelog: boolean = true;
  constructor(
    private router: Router,
    public commfunc: Commfunc,
    private creditCcmService: CreditCcmService,
    private confirmationService: ConfirmationService,
  ) { }
  sendViewParmas: any;
  ngOnInit() {
    this.query('queryBtn');
    this.getCodeValues();
  }
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.businessTypeList = codeValues['MarketingCampaigntType'];
  }
  // 重置
  resetBtn() {
    this.viewRelationBean = new ViewRelationBean();
  }
  // 查询
  query(params?: string) {
    if (params === 'queryBtn') {
      this.pageBean.startIndex = 0;
      this.pageBean.currentPage = 1;
    }
    const queryParams = {
      'businessType': this.viewRelationBean.businessType,
      'productNo': this.viewRelationBean.productNo,
      'roleId': this.viewRelationBean.roleId,
      'viewConfigId': this.viewRelationBean.viewConfigId,
      'viewIndex': this.pageBean.currentPage,
      'viewSize': this.pageBean.pageSize,
    };
    this.creditCcmService.queryViewRelations(queryParams).subscribe(data => {
      if (data.returnCode.code === 'success') {
        this.pageBean.queryList = data.list;
        this.pageBean.totalRecords = data.totalSize;
      } else {
        this.msgs = [{ severity: 'error', summary: '失败', detail: data.returnCode.message }];
      }
    });
  }
  add() {
    this.router.navigate(['/pages/tzb/custom/credit-ccm/viewRelationAdd']);
  }

  paginate(event) {
    this.pageBean.currentPage = event.page + 1;
    this.pageBean.pageSize = event.rows;
    this.pageBean.startIndex = event.first;
    this.query();
  }

  delete() {
    if (!this.pageBean.selectedList || this.pageBean.selectedList.length < 1) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请至少选择一条数据' }];
      return;
    }
    this.confirmationService.confirm({
      message: '确定要删除这些数据吗？',
      accept: () => {
        const viewRelationIdList = [];
        for (const temp of this.pageBean.selectedList) {
          viewRelationIdList.push(temp['viewRelationId']);
        }
        const params = {
          'viewRelationIds': viewRelationIdList
        };
        this.creditCcmService.deleteViewRelations(params).subscribe(data => {
          if (data.returnCode.code === 'success') {
            this.query();
          } else {
            this.msgs = [{ severity: 'error', summary: '失败', detail: data.returnCode.message }];
          }
        });
      },
      reject: () => {
        this.msgs = [{ severity: 'info', summary: '删除', detail: '你选择了拒绝删除！' }];
      }
    });
  }

  // 选择产品
  choosePro() {
    debugger;
    console.log("____________selectProductDisplay______________");
    this.selectProductDisplay = true;
  }

  productCall(event){
    debugger;
    this.selectProductDisplay = false;
    // by yuanzhenghe
    var productId: string = '';//产品id
	  var productName: string = '';//产品名称
    for (const product of event) {
      productName += product.productName + "; ";
      productId += product.productId + "; ";
      console.log(productId);
      console.log(productName);
    }
    this.viewRelationBean.productName = productName;
   // this.viewRelationBean.productName = event[0].productName;
   // this.viewRelationBean.productNo = event[0].productId;
    this.viewRelationBean.productNo = productId;
  }

}
