import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { CreditGuarantorBean } from '../../bean/credit-guarantor.bean';
import { Message } from 'primeng/primeng';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
@Component({
    selector: 'app-credit-guarantor-add',
    templateUrl: './credit-guarantor-add.component.html',
    styleUrls: ['./credit-guarantor-add.component.scss'],
    providers: [CreditCcmService]
})
// 新增担保人信息
// tslint:disable-next-line:component-class-suffix
export class CreditGuarantorAdd implements OnInit {
    // 获取父组件传值：facProcessQuestionId
    @Input() param_data: any;
    @Output() closeDialog = new EventEmitter();
    // 过程问题id
    facProcessQuestionId = '';
    // 提示信息
    msgs: Message[] = [];
    // 担保人信息bean
    creditGuarantorBean: CreditGuarantorBean = new CreditGuarantorBean();
    // 构造器
    constructor(private creditCcmService: CreditCcmService) { }
    // 初始化方法
    ngOnInit() {
        if (this.param_data !== '') {
            this.facProcessQuestionId = this.param_data;
            this.queryGuarantorDetail(this.facProcessQuestionId);
        }
    }
    // 根据客户号查询客户信息
    queryCusts() {
        const params = {
            'custNo': this.creditGuarantorBean.guaranteeCustNo
        };
        // 调用查询客户中心接口，查询客户相关信息
        this.creditCcmService.queryCustNeedInfo(params).subscribe(data => {
            if (data.Service.Header.RetStatus === 'S') {
                this.creditGuarantorBean.guaranteeCustNo = data.Service.Body.custNo;
                this.creditGuarantorBean.guaranteeCustName = data.Service.Body.custName;
            }
        });
    }

    // 新增担保人
    addCreditGuarantor() {
        const params = {
            'facProcessQuestionId': this.facProcessQuestionId,
            'facProcessId': '100010',
            'questionType': 'guaranteeInfo',
            'questionReslponse': this.creditGuarantorBean
        };
        this.creditCcmService.saveFacProcessQuestion(params).subscribe(data => {
            if (data.Service.Header.RetStatus === 'S') {
                this.msgs = [{ severity: 'success', summary: '提示：', detail: '保存成功' }];
            }
        });
    }
    toClose() {
        this.closeDialog.emit();
    }
    // 担心详细信息查询
    queryGuarantorDetail(facProcessQuestionId) {
        const param = {
            'facProcessId': '100010',
            'questionType': 'guaranteeInfo',
            'facProcessQuestionId': facProcessQuestionId
        };
        const first1 = [];
        this.creditCcmService.queryFacProcessQuestions(param).subscribe(data => {
            if (data.Service.Header.RetStatus === 'S') {
                const questionReslponse = JSON.parse(data.Service.Body.list[0].questionReslponse);
                this.creditGuarantorBean.guaranteeCustNo = questionReslponse.guaranteeCustNo;
                this.creditGuarantorBean.guaranteeCustName = questionReslponse.guaranteeCustName;
                this.creditGuarantorBean.contractType = questionReslponse.contractType;
                this.creditGuarantorBean.maxAmountLoanType = questionReslponse.maxAmountLoanType;
                this.creditGuarantorBean.maxAmountLoanNo = questionReslponse.maxAmountLoanNo;
                this.creditGuarantorBean.currency = questionReslponse.currency;
                this.creditGuarantorBean.guaranteeAmt = questionReslponse.guaranteeAmt;
                this.creditGuarantorBean.guaranteeAbility = questionReslponse.guaranteeAbility;
                this.creditGuarantorBean.guaranteeQualificate = questionReslponse.guaranteeQualificate;
                this.creditGuarantorBean.guaranteeRelation = questionReslponse.guaranteeRelation;
                this.creditGuarantorBean.guaranteeConclusion = questionReslponse.guaranteeConclusion;
                this.creditGuarantorBean.startDate = questionReslponse.startDate;
                this.creditGuarantorBean.endDate = questionReslponse.endDate;
                this.creditGuarantorBean.guaranteeWish = questionReslponse.guaranteeWish;
                this.creditGuarantorBean.remark = questionReslponse.remark;
            }
        });
    }
    // 110000
}
