import { Component, OnInit, ChangeDetectorRef, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CommonHttpService } from 'app/pages/tzb/common/http/common.http.service';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { QueryUserListBean } from './bean/transform-query-cust.bean';

@Component({
    selector: 'transform-query-cust',
    templateUrl: './transform-query-cust.component.html',
    styleUrls: ['./transform-query-cust.component.scss'],
    providers: [Commfunc, ConfirmationService, CusOperationHttpService]
})
export class TransformQueryCustComponent implements OnInit, OnChanges {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Input() inValue: any;

    first: number = 0;
    tableData: any;
    totalNum: number = 0;
    personArr: any = [];
    msgs: Message[] = [];
    queryUserListBean: QueryUserListBean = new QueryUserListBean();
    //选择数据
    chooseData: any = [];
    checkAll: boolean = false;
    constructor(
        public httpService: CusOperationHttpService,
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) { }

    ngOnInit() {


        if (this.inValue) {
            if (this.inValue.tellerStatus) {
                this.queryUserListBean.tellerStatus = this.inValue.tellerStatus;
                this.queryUserListBean.partyId = this.inValue.orgId;
            } else {
                this.queryUserListBean.partyId = this.inValue;
                this.queryUserListBean.tellerStatus = undefined;
            }
        }
        this.queryUserListBean.pageSize = 10;
        this.queryUserListBean.pageNum = 1;
        this.queryClick();
    }
    ngOnChanges() {
        if (this.inValue) {
            if (this.inValue.tellerStatus) {
                this.queryUserListBean.tellerStatus = this.inValue.tellerStatus;
                this.queryUserListBean.partyId = this.inValue.orgId;
            } else {
                this.queryUserListBean.partyId = this.inValue;
                this.queryUserListBean.tellerStatus = undefined;
            }
        }
        this.queryUserListBean.pageSize = 10;
        this.queryUserListBean.pageNum = 1;
        this.queryClick();
    }
    // 初始查询
    queryScale() {
        this.queryUserListBean.pageSize = 10;
        this.queryUserListBean.pageNum = 1;
        this.first = 0;
        this.queryClick();
    }

    // 分页查询
    queryClick() {

        if (this.queryUserListBean.tellerName == '') {
            this.queryUserListBean.tellerName = undefined;
        }
        if (this.queryUserListBean.tellerId == '') {
            this.queryUserListBean.tellerId = undefined;
        }

        this.httpService.queryTellersByPartyIdAndRoleId(this.queryUserListBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.tellerList;
                let tmpTotal = data.total;
                this.totalNum = tmpTotal * 1;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    // 分页
    paginate(event) {
        this.queryUserListBean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.queryUserListBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.queryUserListBean.pageSize;
        this.queryClick();
    }

    //贷款业务全选按钮
    checkAllClick() {
        this.chooseData = [];
        for (let i = 0; i < this.tableData.length; i++) {
            if (this.checkAll == true) {
                this.tableData[i].checkBox = true;
                this.chooseData.push(this.tableData[i]);
            }
            else {
                this.chooseData = [];
                this.tableData[i].checkBox = false;
            }
        }
    }
    //贷款业务单选按钮
    checkBoxClick(i, data) {
        if (data.checkBox) {
            this.chooseData.push(data)
        } else {
            this.chooseData.forEach((item, i) => {
                if (item == data) {
                    this.chooseData.splice(i, 1)
                }
            });
        }
        if (this.chooseData.length == this.tableData.length) {
            this.checkAll = true;
        } else {
            this.checkAll = false;
        }
    }
    //确认传值
    commit() {
        this.outValue.emit(this.chooseData);
    }
}
