import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { TransformationPlanHttpService } from '../http/transformation-plan.http.service';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { TransformLoanPlanBean, TenTypeBean } from '../bean/transform-loan-plan.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TZB_HTTP_XIN, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { Router, ActivatedRoute } from '@angular/router';
import { CreditCcmService } from '../../common/http/credit-ccm.http.service';
import { DecimalPipe } from '@angular/common';
@Component({
    selector: 'transform-loan-detail',
    templateUrl: './transform-loan-detail.component.html',
    styleUrls: ['./transform-loan-detail.component.scss'],
    providers: [TransformationPlanHttpService, Commfunc, DecimalPipe]
})
export class TransformLoanDetailComponent implements OnInit {
    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    first: number = 0;
    msgs: Message[] = [];
    dataTable: any = [];
    transformLoanPlanBean: TransformLoanPlanBean = new TransformLoanPlanBean();
    surveyList: any = [];//调查人列表
    firstApproverList: any = [];//第一审批人列表
    otherApproverList: any = [];//其他审批人列表
    guaranteeInfoList: any = [];//担保人列表
    debtMessageArray: any = [];//借据信息列表
    pageSize: number = 10;
    pageNum: number = 1;
    total: number;
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载
    convertPlanHistoryArray: any = [];//历史转化方案列表
    tenTypeBean: TenTypeBean = new TenTypeBean();
    user: any;
    fileName: any;
    fileUrl: any;
    orgId: string = '';
    userId: string = '';
    commonHeader: any;
    last_transform: boolean = false;
    code: any;
    convertObjectOptions: any = [];//转化对象
    convertEffectRatio: any = [];//转化成效率
    question: any = [];//是否
    tenTypeTransform: any = [];//十级分类
    guaranteeModeOptions: any = [];//主担保方式
    tellID: string;//工号
    tellName: string;//姓名
    branchApprover: string;
    institutionalApprover: string;
    headofficeApprover: string;
    showDisabled: boolean = true;//不给修改
    param: any;
    user2: any;
    userName: string = '';
    showTime: string;
    constructor(
        public httpService: TransformationPlanHttpService,
        public commfunc: Commfunc,
        public router: Router,
        private creditCcmService: CreditCcmService,
        public decimalPipe: DecimalPipe,
        public routeInfo: ActivatedRoute,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
    }

    ngOnInit() {
        this.codeValues();
        this.convertObjectOptions = this.code['convertObjectTransform'];//转化对象
        this.convertEffectRatio = this.code['convertEffectRatioTransform'];//转化成效率
        this.question = this.code['transformQuestion'];//是否
        this.tenTypeTransform = this.code['TransformTenType'];//十级分类
        this.guaranteeModeOptions = this.code['guaranteeModeTransform'];//主担保方式
        this.commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (!this.inValue) {
            //console.log(this.routeInfo.snapshot.params)
            this.inValue = this.routeInfo.snapshot.params;
        } else if (this.inValue.value_param.transferCreditData['taskRate'] != 1 && this.inValue.value_param.transferCreditData['flag'] != '2') {
            this.showDisabled = false;//给改
        }
        this.query();
        this.queryDebtMessageList();
        this.queryConvertPlanHistory();
        this.queryBase();//担保信息

    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    query() {
        let param = {
            contractNo: this.inValue.contractNo,
            tranSeqNo: this.inValue.tranSeqNo,
        }
        this.httpService.queryConvertPlanDetailInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.fileName = data.resultList[0].fileName;
                this.fileUrl = data.resultList[0].filePath;
                if (data.resultList && data.resultList.length > 0) {
                    this.transformLoanPlanBean = data.resultList[0];
                    /**转化金额格式 */
                    this.transformLoanPlanBean.convertContractAmount = this.decimalPipe.transform(data.resultList[0].convertContractAmount / 1, '1.2-2');
                    this.transformLoanPlanBean.loanAmt = this.decimalPipe.transform(data.resultList[0].loanAmt / 1, '1.2-2');
                    this.transformLoanPlanBean.loanBalance = this.decimalPipe.transform(data.resultList[0].loanBalance, '1.2-2');
                    this.transformLoanPlanBean.rate = this.decimalPipe.transform(data.resultList[0].rate / 1, '1.2-2') + "‰";

                    this.otherApproverList = data.resultList[0].otherApproverList;
                    this.firstApproverList = data.resultList[0].firstApproverList;
                    this.surveyList = data.resultList[0].surveyList;
                    this.queryLastMoney();//回显上笔金额
                    if (this.transformLoanPlanBean.branchApprover && this.transformLoanPlanBean.branchApprover != '') {
                        this.queryUser(this.transformLoanPlanBean.branchApprover, 1);
                    }
                    if (this.transformLoanPlanBean.institutionalApprover && this.transformLoanPlanBean.institutionalApprover != '') {
                        this.queryUser(this.transformLoanPlanBean.institutionalApprover, 2);
                    }
                    if (this.transformLoanPlanBean.headofficeApprover && this.transformLoanPlanBean.headofficeApprover != '') {
                        this.queryUser(this.transformLoanPlanBean.headofficeApprover, 3);
                    }
                } else {
                    this.otherApproverList = [];
                    this.firstApproverList = [];
                    this.surveyList = [];
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
    }
    //借据列表查询
    queryDebtMessageList() {
        let param = {
            pageSize: this.pageSize,
            pageNum: this.pageNum,
            contractNo: this.inValue.contractNo
        }
        this.httpService.queryContractDebtMessageInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.debtMessageList && data.debtMessageList.length > 0) {
                    this.debtMessageArray = data.debtMessageList;
                } else {
                    this.debtMessageArray = [];
                }
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
    }
    //借据列表翻页
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.queryDebtMessageList();
    }
    //历史转化方案列表
    queryConvertPlanHistory() {
        let numSize;
        if (this.inValue.contractNo.length >= 16) {
            numSize = '1';
        } else {
            numSize = '0';
        }
        let showTimeFlag = this.inValue.value_param.transferCreditData['startTime']
        let param = {
            contractNo: this.inValue.contractNo,
            numberType: numSize,
            startTime: showTimeFlag,
        }
        this.httpService.queryHistotyConversionListInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.convertPlanHistoryList && data.convertPlanHistoryList.length > 0) {
                    this.convertPlanHistoryArray = data.convertPlanHistoryList;
                } else {
                    this.convertPlanHistoryArray = [];
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
    }
    //回显担保信息
    queryBase() {
        let param = {
            contractNo: this.inValue.contractNo,
        }
        this.httpService.queryLoanContractDetailsInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.resultList && data.resultList.length > 0) {
                    this.guaranteeInfoList = data.resultList[0].guaranteeInfoList;
                } else {
                    this.guaranteeInfoList = [];
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
    }
    //风险处置
    toTransformURL(col) {
        if (col.disposalNumber) {
            let timestamp = new Date().getTime();
            let target = "custLinkForm" + timestamp;
            this.commfunc.createBlank(target);
            this.tenTypeBean.userId = this.userId;
            this.tenTypeBean.sysCode = '122';
            this.tenTypeBean.casServerLoginUrl = 'http://10.1.39.140:8011/searchPaymentTool';
            this.tenTypeBean.serverName = 'ywsn';
            this.tenTypeBean.validateType = '0';
            this.tenTypeBean.extraInfo = 'ywsn';
            this.tenTypeBean.encryptType = 'HS256';
            this.httpService.tokenApply(this.tenTypeBean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    let tokens = data.tokenCode;
                    let url;
                    if (col.numberType == '0') {
                        //贷款
                        url = TZB_HTTP_XIN + "plats/FrontController?token=" + tokens + "&suid=" + this.userId + "&czbh=" + col.disposalNumber + "&urlParam=/plats/FrontController?commandType=9904|moduleCode=27548A5263C91D89A2D6B2D8EF5061FC|moduleName=|main_moduleName=|moduleTable=F9B2E807758C7365FE7EE21FEABF7005|ActionCode=view|ActionType=VIEW|isProcModule=no|main_moduleCode=27548A5263C91D89A2D6B2D8EF5061FC|main_moduleTable=F9B2E807758C7365FE7EE21FEABF7005";
                    } else if (col.numberType == '1') {
                        //信用卡
                        url = TZB_HTTP_XIN + "plats/FrontController?token=" + tokens + "&suid=" + this.userId + "&czbh=" + col.disposalNumber + "&urlParam=/plats/FrontController?commandType=9904|moduleCode=27548A5263C91D89A2D6B2D8EF5061FC|moduleName=|main_moduleName=|moduleTable=F9B2E807758C7365FE7EE21FEABF7007|ActionCode=view|ActionType=VIEW|isProcModule=no|main_moduleCode=27548A5263C91D89A2D6B2D8EF5061FC|main_moduleTable=F9B2E807758C7365FE7EE21FEABF7007";
                    }
                    let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
                    let closeOnLoseFocus = 'closeOnLoseFocus';
                    this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        }
    }
    toLoan(col) {
        if (!col.disposalNumber) {
            this.showTime = col.startTime;
            this.creditCcmService.queryHistotySeqData({ tranSeqNo: col.tranSeqNo }).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.toDoExam(data.pageData.result[0]);
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        } else {
            this.toTransformURL(col);
        }
    }
    //逾期案例
    toDetail() {
        let param = {
            contractNo: this.transformLoanPlanBean.contractNo,
            custNo: this.transformLoanPlanBean.partyIdTo,
            isDetailFlag: true
        }
        window.open('/#/pages/tzb/custom/overdue/overdue-case;param=' + JSON.stringify(param))
        // this.router.navigate(['/pages/tzb/custom/overdue/overdue-case', param])
    }
    // 查询关联页面
    toDoExam(data) {
        data['markFlag'] = '2';
        if (!(data.varMap.taskCategoryId.indexOf('RUM0005') < 0) || !(data.varMap.taskCategoryId.indexOf('RUM0009') < 0)) {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            let post_name = JSON.parse(this.commfunc.getSessionDataCH('postIdList'));
            let products = '';
            if (data.varMap.dataMap.productNo) {
                products = data.varMap.dataMap.productNo;
            } else {
                products = data.varMap.dataMap.productId;
            }
            const params = {
                // 'businessType': data.varMap.dataMap.taskType,
                productNo: products,
                roleId: post_name,
                viewIndex: 1,
                viewSize: 100,
            };
            //如果flag为‘2’则是从历史进入的
            let flag = '2';
            this.creditCcmService.queryViewRelations(params).subscribe(datas => {
                if (datas.returnCode.code === 'success') {
                    if (datas.list.length > 0) {
                        let viewId = [];
                        data.list.forEach(e => {
                            viewId.push({ a: e.viewConfigId, b: e.viewName });
                        });
                        this.commfunc.setSessionDataCH('viewId', JSON.stringify(viewId));
                        let param, pageParams, littleClassNo;
                        if (!(data.varMap.taskCategoryId.indexOf('RUM0009') < 0)) {
                            for (let i = 0; i < datas.list.length; i++) {
                                if (datas.list[i].viewName.match('卡要素')) {
                                    param = {
                                        'viewConfigId': datas.list[i]['viewConfigId'],
                                        'viewName': datas.list[i]['viewName'],
                                        'flag': true//审批标识
                                    }
                                    pageParams = {
                                        'viewConfigId': datas.list[i]['viewConfigId'],
                                        'viewName': datas.list[i]['viewName'],
                                        'custNo': data.varMap.dataMap.custNo,
                                        'custName': data.varMap.dataMap.custName,
                                        'custRequestNo': data.varMap.custRequestNo,
                                        'productNo': products,
                                        'productName': data.varMap.dataMap.productName,
                                        'isView': '',
                                        'businessType': data.varMap.dataMap.businessType,
                                        'processId': data.processId,
                                        'taskId': data.taskId,
                                        'workCreater': data.workCreater,
                                        'guaranteeType': data.varMap.dataMap.guaranteeType,
                                        'applyAmt': data.varMap.dataMap.applyAmt,
                                        'varMap': data.varMap,
                                        'taskRate': data.taskRate,
                                        'custRequestId': data.varMap.custRequestId,
                                        'endDate': data.varMap.dataMap.endDate,
                                        'taskType': data.taskType,
                                        'stageType': data.varMap.stageType,
                                        'taskName': data.taskName,
                                        'assActorId': data.varMap.dataMap['assActorId'],
                                        'requestSeqNo': data.varMap.requestSeqNo,
                                        'flag': flag,
                                        'processState': data.processState,
                                        'startTime': this.showTime
                                    };
                                    break;
                                }
                            }
                        } else {
                            for (let i = 0; i < datas.list.length; i++) {
                                let str = '卡要素';
                                if (datas.list[i].viewName.indexOf(str) == -1) {
                                    param = {
                                        'viewConfigId': datas.list[i]['viewConfigId'],
                                        'viewName': datas.list[i]['viewName'],
                                    }
                                    pageParams = {
                                        'viewConfigId': datas.list[i]['viewConfigId'],
                                        'viewName': datas.list[i]['viewName'],
                                        'custNo': data.varMap.dataMap.custNo,
                                        'custName': data.varMap.dataMap.custName,
                                        'custRequestNo': data.varMap.custRequestNo,
                                        'productNo': products,
                                        'productName': data.varMap.dataMap.productName,
                                        'isView': '',
                                        'businessType': data.varMap.dataMap.businessType,
                                        'processId': data.processId,
                                        'taskId': data.taskId,
                                        'workCreater': data.workCreater,
                                        'guaranteeType': data.varMap.dataMap.guaranteeType,
                                        'applyAmt': data.varMap.dataMap.applyAmt,
                                        'varMap': data.varMap,
                                        'taskRate': data.taskRate,
                                        'custRequestId': data.varMap.custRequestId,
                                        'endDate': data.varMap.dataMap.endDate,
                                        'stageType': data.varMap.stageType,
                                        'taskName': data.taskName,
                                        'taskType': data.taskType,
                                        'assActorId': data.varMap.dataMap['assActorId'],
                                        'requestSeqNo': data.varMap.requestSeqNo,
                                        'flag': flag,
                                        'processState': data.processState,
                                        'startTime': this.showTime
                                    };
                                    break;
                                }
                            }


                        }
                        littleClassNo = data.littleClassNo;
                        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                        commonHeader['extParam']['littleClassNo'] = littleClassNo;
                        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));

                        this.commfunc.setSessionDataCH('creditDataWorkFlow', JSON.stringify(pageParams))
                        // this.router.navigate(['/pages/tzb/custom/credit-ccm/viewPage', JSON.stringify(param)]);
                        let paramss = JSON.stringify(param)
                        window.open(`/#/pages/tzb/custom/credit-ccm/viewPage/${paramss}`, '_blank')
                    } else {
                        this.msgs = [{ severity: 'info', summary: '错误', detail: '产品和岗位未关联页面,请配置关联页面' }];
                    }

                } else {
                    this.msgs = [{ severity: 'error', summary: '错误', detail: datas.returnCode.message }];
                }
            });

        } else {
            this.commfunc.toDisposeSchedule(data);
        }

    }
    //下载
    down() {
        open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(this.fileName) + "&fileUrl=" + this.fileUrl);
    }
    //查询员工
    queryUser(item, num) {
        let param = {
            pageNum: 1,
            pageSize: 10,
            partyId: this.orgId,
            tellerId: item,
        }
        this.httpService.queryTellersByPartyIdAndRoleId(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.tellerList && data.tellerList.length > 0) {
                    this.tellName = data.tellerList[0].tellerName;
                    if (num == 1) {
                        this.branchApprover = this.tellName;
                    }
                    if (num == 2) {
                        this.institutionalApprover = this.tellName;
                    }
                    if (num == 3) {
                        this.headofficeApprover = this.tellName;
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
    }
    //回显上笔金额
    queryLastMoney() {
        this.httpService.queryLastConvertLoanContractInfo({ contractNo: this.transformLoanPlanBean.contractNo }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.transformLoanPlanBean.convertFrequency = data.convertFrequency;
                if (data.convertFrequency && data.convertFrequency > 1) {
                    this.last_transform = true;
                    this.transformLoanPlanBean.lastConvertContractNo = data.lastConvertContractNo;
                    this.transformLoanPlanBean.lastConvertContractAmount = this.decimalPipe.transform(data.lastConvertContractAmount / 1, '1.2-2');
                    this.transformLoanPlanBean.lastConvertContractBalance = this.decimalPipe.transform(data.lastConvertContractBalance / 1, '1.2-2');
                    this.transformLoanPlanBean.lastLoanDateStart = data.lastLoanDateStart;
                } else {
                    this.last_transform = false;
                    this.transformLoanPlanBean.isLoanrePayment = data.firstConvertTime;
                }
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    //保存
    saveUpdate() {
        let array = {
            primaryId: '',
            partyId: '',//当事人标识
            roleTypeId: '',//角色类型标识 
            partyPostNo: '',//当事人岗位编号
            partyName: '',//当事人名称
            partyDeduction: '',//当事人扣分
            statesat: '',//状态 1：加 2：update  3：减
            partyPostName: '',
        };
        let arrList = [];
        if (this.surveyList && this.surveyList.length > 0) {
            for (let item of this.surveyList) {
                if (item.surveyNo
                    || item.surveyName
                    || item.surveyDeduction) {
                    if (item.primaryId && item.primaryId != '') {
                        array.statesat = '2';
                    } else {
                        array.statesat = '1';
                    }
                    array.primaryId = item.primaryId;
                    array.partyId = item.surveyNo;
                    array.roleTypeId = '00';
                    array.partyPostNo = '';
                    array.partyName = item.surveyName;
                    array.partyDeduction = item.surveyDeduction;
                    let items = JSON.parse(JSON.stringify(array))
                    arrList.push(items);
                }
            }
        }
        if (this.firstApproverList && this.firstApproverList.length > 0) {
            for (let item of this.firstApproverList) {
                if (item.firstApproverNo
                    || item.firstApproverName
                    || item.firstApproverDeduction) {
                    if (item.primaryId && item.primaryId != '') {
                        array.statesat = '2';
                    } else {
                        array.statesat = '1';
                    }
                    array.primaryId = item.primaryId;
                    array.partyId = item.firstApproverNo;
                    array.roleTypeId = '01';
                    array.partyPostNo = '';
                    array.partyName = item.firstApproverName;
                    array.partyDeduction = item.firstApproverDeduction;
                    let items = JSON.parse(JSON.stringify(array))
                    arrList.push(items);
                }
            }
        }
        if (this.otherApproverList && this.otherApproverList.length > 0) {
            for (let item of this.otherApproverList) {
                if (item.otherApproverNo
                    || item.otherApproverPostNo
                    || item.otherApproverPostName
                    || item.otherApproverDeduction
                    || item.otherApproverName) {
                    if (item.primaryId && item.primaryId != '') {
                        array.statesat = '2';
                    } else {
                        array.statesat = '1';
                    }
                    array.primaryId = item.primaryId;
                    array.partyId = item.otherApproverNo;
                    array.roleTypeId = '02';
                    array.partyPostNo = item.otherApproverPostNo;
                    array.partyPostName = item.otherApproverPostName;
                    array.partyName = item.otherApproverName;
                    array.partyDeduction = item.otherApproverDeduction;
                    let items = JSON.parse(JSON.stringify(array))
                    arrList.push(items);
                }
            }
        }
        if (this.guaranteeInfoList && this.guaranteeInfoList.length > 0) {
            for (let item of this.guaranteeInfoList) {
                array.primaryId = item.primaryId;
                array.partyId = item.guaranteeCustNo;
                array.roleTypeId = '03';
                array.partyName = item.guaranteeCustName;
                array.statesat = '2';
                let items = JSON.parse(JSON.stringify(array))
                arrList.push(items);
            }
        }
        /**金额转化 */
        if (this.transformLoanPlanBean.loanAmt) {
            this.transformLoanPlanBean.loanAmt = this.commfunc.transform(this.transformLoanPlanBean.loanAmt).toString();
        }
        if (this.transformLoanPlanBean.loanBalance) {
            this.transformLoanPlanBean.loanBalance = this.commfunc.transform(this.transformLoanPlanBean.loanBalance).toString();
        }
        if (this.transformLoanPlanBean.lastConvertContractAmount) {
            this.transformLoanPlanBean.lastConvertContractAmount = this.commfunc.transform(this.transformLoanPlanBean.lastConvertContractAmount).toString();
        }
        if (this.transformLoanPlanBean.lastConvertContractBalance) {
            this.transformLoanPlanBean.lastConvertContractBalance = this.commfunc.transform(this.transformLoanPlanBean.lastConvertContractBalance).toString();
        }
        if (this.transformLoanPlanBean.rate) {
            let rate = this.transformLoanPlanBean.rate;
            if (this.transformLoanPlanBean.rate.indexOf('‰') != -1) {
                rate = this.transformLoanPlanBean.rate.slice(0, this.transformLoanPlanBean.rate.length - 1);
            }
            this.transformLoanPlanBean.rate = this.commfunc.transform(rate).toString();
        }
        this.transformLoanPlanBean.tellerName = this.userName;
        this.transformLoanPlanBean.listBox = arrList;
        this.transformLoanPlanBean.operateType = '2';
        this.transformLoanPlanBean.custRequestId = this.inValue.tranSeqNo;
        this.transformLoanPlanBean.taskRate = this.inValue.taskRate;
        this.httpService.maintainConvertPlanInfo(this.transformLoanPlanBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '修改成功!' });
                this.outValue.emit(false);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                return
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
    }
}
