import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

import { Message, ConfirmationService } from 'primeng/primeng';

import { PageBean } from '../bean/page.bean';
import { ViewConfigBean } from '../bean/view-config.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'app-common-view-add',
    templateUrl: './common-view-add.component.html',
    styleUrls: ['./common-view-add.component.scss'],
})

export class CommonViewAddComponent implements OnInit {

    viewConfigBean = new ViewConfigBean();

    pageBean = new PageBean();

    hiddenBookmark = true;

    showSaveBtn = true;

    sendBookMarkParmas: any;

    showAddBookMark = false;

    msgs: Message[] = [];

    SaveSuccessMsgs: Message[] = [];

    myformChild: FormGroup = this.fb.group({
        'viewConfigId': new FormControl(''),
        'viewName': new FormControl('', [Validators.pattern(/^[a-zA-Z0-9\u4e00-\u9fa5]{1,30}$/), Validators.required]),
    }); //校验

    constructor(
        private router: Router,
        public fb: FormBuilder,
        public commfunc: Commfunc,
        private location: Location,
        private creditCcmService: CreditCcmService,
        private confirmationService: ConfirmationService,
    ) { }

    ngOnInit() {
        // 获取用户信息
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.viewConfigBean.tellerNo = commonHeader['userId'];
        this.viewConfigBean.tellerOrgNo = commonHeader['orgId'];
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.viewConfigBean.tellerName = chName['userName'];
        this.viewConfigBean.tellerOrgName = chName['orgName'];
    }

    // 返回
    backBtn() {
        this.location.back();
    }

    // 提交之前做一次校验
    preValidateForSubmit() {
        //校验的
        for (const i in this.myformChild.controls) this.myformChild.controls[i].markAsDirty();
        //处理是否提交
        for (const i in this.myformChild.controls) {
            if (!this.myformChild.controls[i].valid) return false;
        }
        return true;
    }

    // 保存
    saveBtn() {
        if (!this.preValidateForSubmit()) return;
        // 调用接口保存
        this.creditCcmService.createViewConfig(this.viewConfigBean).subscribe(data => {
            if (data.returnCode.code === 'success') {
                if (data.flag === '1') {
                    this.msgs = [{ severity: 'warn', summary: '提示：', detail: '名称已经存在' }];
                    return;
                }
                this.viewConfigBean.viewConfigId = data.viewConfigId;
                this.viewConfigBean.viewName = data.viewName;
                this.viewConfigBean.viewConfigSeq = data.viewConfigSeq;
                this.hiddenBookmark = false;
                this.showSaveBtn = false;
                this.msgs = [{ severity: 'success', summary: '提示：', detail: '页面保存成功!' }];
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        });
    }


    // 引入页签
    importBookMark() {
        this.showAddBookMark = true;
        this.sendBookMarkParmas = { 'view': 'view' };
    }
    // 删除页签
    delete() {
        if (!this.pageBean.selectedList) {
            this.msgs = [{ severity: 'info', summary: '提示', detail: '请至少选择一条数据！' }];
            return;
        }
        this.confirmationService.confirm({
            message: '确定要删除这些数据吗？',
            accept: () => {
                const tempList = [];
                for (const item of this.pageBean.queryList) {
                    if (this.pageBean.selectedList && this.pageBean.selectedList.findIndex((temp) => temp === item) === -1) {
                        tempList.push(item);
                    }
                }
                this.pageBean.queryList = tempList;
            },
            reject: () => {
                this.msgs = [{ severity: 'info', summary: '删除', detail: '你选择了拒绝删除！' }];
            }
        });
    }
    addBookMarkCallback(event) {
        this.showAddBookMark = false;
        if (event === 'empty') {
            return;
        } else if (this.pageBean.queryList && this.pageBean.queryList.length === 0) {
            for (const item of event) {
                this.pageBean.queryList.push(item);
            }
        } else {
            for (const item of event) {
                if (this.pageBean.queryList &&
                    this.pageBean.queryList.findIndex((temp) => temp['bookmarkId'] === item['bookmarkId']) === -1) {
                    this.pageBean.queryList.push(item);
                }
            }
        }

        // 调用查询接口
    }
    // 上移
    moveUpIcon(item): void {
        const order = this.pageBean.queryList.indexOf(item);
        if (order === 0) {
            this.msgs = [{ severity: 'info', summary: '提示：', detail: '当前已是第一个！' }];
        } else {
            const temp = this.pageBean.queryList[order - 1];
            this.pageBean.queryList[order - 1] = this.pageBean.queryList[order];
            this.pageBean.queryList[order] = temp;
        }
    }
    // 下移
    moveDownIcon(item): void {
        const order = this.pageBean.queryList.indexOf(item);
        if (order === this.pageBean.queryList.length - 1) {
            this.msgs = [{ severity: 'info', summary: '提示：', detail: '当前已是最后一个！' }];
        } else {
            const temp = this.pageBean.queryList[order + 1];
            this.pageBean.queryList[order + 1] = this.pageBean.queryList[order];
            this.pageBean.queryList[order] = temp;
        }
    }
    // 确定-保存页面配置
    confirm() {
        if (this.pageBean.queryList.length < 1) {
            this.msgs = [{ severity: 'info', summary: '提示：', detail: '请至少引入一条页签！' }];
            return;
        }
        const tempList = [];
        for (let i = 0; i < this.pageBean.queryList.length; i++) {
            const temp = {
                'bookmarkId': this.pageBean.queryList[i]['bookmarkId'],
                'bookmarkName': this.pageBean.queryList[i]['bookmarkName'],
                'bookmarkOrder': (i + 1),
            };
            tempList.push(temp);
        }

        // 调用接口
        const params = {
            'viewConfigId': this.viewConfigBean.viewConfigId,
            'viewName': this.viewConfigBean.viewName,
            'tellerOrgNo': this.viewConfigBean.tellerNo,
            'tellerOrgName': this.viewConfigBean.tellerOrgName,
            'tellerNo': this.viewConfigBean.tellerNo,
            'tellerName': this.viewConfigBean.tellerName,
            'list': tempList,
        };
        this.creditCcmService.addBatchViewConfig(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.SaveSuccessMsgs = [{ severity: 'success', summary: '提示：', detail: '页面关联页签保存成功' }];
            } else {
                this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
            }
        });
    }
     // 分页按钮事件
    // paginate(event) {
    //     this.pageBean.currentPage = event.page + 1;
    //     this.pageBean.pageSize = event.rows;
    //     this.pageBean.startIndex = event.first;
    //     this.query();
    // }
}
