    // 更改是否集团客户
    changeBlocCust() {
        this.creditCustBean.blocCustCode = undefined;
        this.creditCustBean.blocCustName = undefined;
        if (this.creditCustBean.whetherBlocCust == '1') {//是
            this.isBlocCust = true;
        } else {
            this.isBlocCust = false;
        }
    }
    // 校验集团编号
    docheck(event) {
        if (event.target.value != '') {
            // 校验客户号
            var reg = /^[0-9]*$/;
            if (!reg.test(event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入数字' }];
                this.creditCustBean.blocCustName = undefined;
                event.target.value = '';
            } else {
                if (event.target.value.length == 10) {
                    this.queryCust();
                } else {
                    this.creditCustBean.blocCustName = undefined;
                }
            }
        }
    }

    // 根据客户号查询客户名称(不为null并且为10位)
    queryCust() {
        let param = {
            custNo: this.creditCustBean.blocCustCode
        }
        let ob2 = this.httpService.selectCustBriefInfo(param);
        ob2.subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.creditCustBean.blocCustName = data.custName;
                // 校验客户名称
                if (this.creditCustBean.blocCustName == undefined || this.creditCustBean.blocCustName == '' || this.creditCustBean.blocCustName == null) {
                    this.creditCustBean.blocCustName = null;
                } else {
                }
            } else {
                this.creditCustBean.blocCustName = null;
            }
        }, error => {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '该集团客户不存在' }];
            this.creditCustBean.blocCustName = null;
        })
    }
