    /**
     * 判断抵押金额，可用余额，最高额合同金额
     */
    pledgeAmtChange(mod) {
        if(mod && mod == '1'){
            if (this.mortgageBean.pledgeAmt && this.mortgageBean.pledgeContractBalance) {
                let pledgeAmt1 = this.mortgageBean.pledgeAmt;
                if(pledgeAmt1 && pledgeAmt1.toString().indexOf(",")!=-1){
                    pledgeAmt1 = this.mortgageBean.pledgeAmt.replace(/\,/g, '');
                }
                let pledgeContractBalance1 = this.mortgageBean.pledgeContractBalance;
                if(pledgeContractBalance1 && pledgeContractBalance1.toString().indexOf(",")!=-1){
                    pledgeContractBalance1 = this.mortgageBean.pledgeContractBalance.replace(/\,/g, '');
                }
                if (Number(pledgeAmt1) > Number(pledgeContractBalance1)) {
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '抵押金额应小于可用金额，请重新录入' });
                    this.mortgageBean.pledgeAmt = '';
                    return;
                } else if (Number(pledgeAmt1) < 0) {
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '抵押金额应大于0，请重新录入' });
                    this.mortgageBean.pledgeAmt = '';
                    return;
                }
            }
        }
    }

    //登记结束日期判断
    dateCheck(flag) {
        var registerStartDate = new Date(this.mortgageBean.registerStartDate);
        var registerEndDate = new Date(this.mortgageBean.registerEndDate);
        var guaranteeStartDate = new Date(this.mortgageBean.guaranteeStartDate);
        var guaranteeEndDate = new Date(this.mortgageBean.guaranteeEndDate);
        if (flag) {
            if (flag == 'registerStartDate') {
                if (registerStartDate && registerEndDate &&
                    registerStartDate > registerEndDate) {
                    this.mortgageBean.registerStartDate = '';
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '登记开始日期不能大于登记到期日期，请重新录入' });
                    //this.registerEndDateFlag = true;
                    return;
                }
                if (registerStartDate && guaranteeStartDate &&
                    registerStartDate > guaranteeStartDate) {
                    this.mortgageBean.registerStartDate = '';
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '登记开始日期不能大于担保开始日期，请重新录入' });
                    //this.registerEndDateFlag = true;
                    return;
                }
            } else if (flag == 'registerEndDate') {
                if (registerStartDate && registerEndDate &&
                    registerStartDate > registerEndDate) {
                    this.mortgageBean.registerEndDate = '';
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '登记到期日期不能小于登记开始日期，请重新录入' });
                    //this.registerEndDateFlag = true;
                    return;
                }
                if (registerEndDate && guaranteeEndDate &&
                    registerEndDate < guaranteeEndDate) {
                    this.mortgageBean.registerEndDate = '';
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '登记到期日期不能小于担保到期日期，请重新录入' });
                    //this.registerEndDateFlag = true;
                    return;
                }
            } else if (flag == 'guaranteeStartDate') {
                if (guaranteeStartDate && guaranteeEndDate &&
                    guaranteeStartDate > guaranteeEndDate) {
                    this.mortgageBean.guaranteeStartDate = '';
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '担保开始日期不能大于担保到期日期，请重新录入' });
                    //this.registerEndDateFlag = true;
                    return;
                }
                if (guaranteeStartDate && registerStartDate &&
                    guaranteeStartDate > registerStartDate) {
                    this.mortgageBean.guaranteeStartDate = '';
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '担保开始日期不能大于登记开始日期，请重新录入' });
                    //this.registerEndDateFlag = true;
                    return;
                }
            } else if (flag == 'guaranteeEndDate') {
                if (guaranteeStartDate && guaranteeEndDate &&
                    guaranteeStartDate > guaranteeEndDate) {
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '担保到期日期不能小于担保开始日期，请重新录入' });
                    this.mortgageBean.guaranteeEndDate = '';
                    //this.registerEndDateFlag = true;
                    return;
                }
                if (guaranteeEndDate && registerEndDate &&
                    guaranteeEndDate > registerEndDate) {
                    this.msgs.push({ severity: 'error', summary: '错误', detail: '担保到期日期不能大于登记到期日期，请重新录入' });
                    this.mortgageBean.guaranteeEndDate = '';
                    //this.registerEndDateFlag = true;
                    return;
                }
            }

        }
    }

