export class BookmarkBean {
    bookmarkSeq: string; // 页签主键
    bookmarkId: string; // 页签编号
    bookmarkName: string; // 页签名称
    basicSourceId: string; // 资源编号
    sourceOrder: string; // 资源序号
    basicSourceName: string; // 资源名称
    isInvalid: string; // 是否有效
    tellerOrgNo = '';  // 经办机构
    tellerOrgName = '';  // 经办机构名称
    tellerNo = '';  // 经办人编号
    tellerName = ''; // 经办人名称
    constructor(bookmarkId?, bookmarkName?) {
        this.bookmarkId = bookmarkId;
        this.bookmarkName = bookmarkName;
    }
}
