import { Component, Input, Output, OnInit, EventEmitter } from '@angular/core';

import { Message } from 'primeng/primeng';

import { ParamsBean } from 'app/pages/tzb/custom/credit-ccm/bean/params.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'app-credit-rate-detail',
    templateUrl: './credit-rate-detail.component.html',
    styleUrls: ['./credit-rate-detail.component.scss']
})

// tslint:disable-next-line:component-class-suffix
export class CreditRateDetail implements OnInit {

    startIndex: any;
    pageSize: any;
    pageNum: any;
    queryList: any;
    totalRecords: any;
    workFlowResultType: any;
    code: any;
    userId: any;
    // 构造器
    constructor(private creditCcmService: CreditCcmService, public commfunc: Commfunc, ) { }

    // 父组件传过来的审批ID参数
    @Input() invaule: any;
    @Input() passids: any;
    // 子组件返回父组件消息
    @Output() outValue = new EventEmitter();

    // 消息
    msgs: Message[] = [];

    // 分页参数
    paramsBean = new ParamsBean();

    // 初始加载
    ngOnInit() {
       console.log(this.invaule);
        this.codeValues()
        this.workFlowResultType = this.code['WorkFlowResultType'];
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.queryCreditApproveDetail();
    }

    // 查看审批详情
    queryCreditApproveDetail() {
        if (this.invaule.accrualScaleLevel) {
            if (this.invaule.guaranteeMode) {
                this.creditCcmService.rateTrialInternalService(this.invaule).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        let queryList = [];
                        debugger
                        data.intestArray.forEach(item => {
                            if (item.accrualScaleName != null) {
                                // let firstStr = item.accrualScaleName.slice(0, 1);
                                // if (String(Number(firstStr)) != 'NaN') {
                                //     queryList.push(item);
                                // }
                                queryList.push(item);
                            } else {
                                queryList.push(item);
                            }

                        });
                        this.queryList = queryList;
                        // this.totalRecords = data.totalCount;
                    } else {
                        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
                        return;
                    }
                });
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: '担保方式未选择！' }];
            }
        } else {
            this.msgs = [{ severity: 'error', summary: '错误', detail: '积数比级别未选择！' }];
        }
    }

    // 审批详情分页事件
    // paginate(event) {
    //     this.pageNum = event.page + 1;
    //     this.pageSize = event.rows;
    //     this.startIndex = event.first;
    //     this.queryCreditApproveDetail();
    // }

    choiceRates(item) {
        let items = {
            item: item,
            passids: this.passids,
        };
        this.outValue.emit(items);
    }
    // 关闭审批详情弹框
    closeCreditApproveDetail() {
        this.outValue.emit();
    }

    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }
}
