	//家庭信息
	queryFarmily() {
		if (this.creditCustBean.address3 && this.creditCustBean.address3 != null && this.creditCustBean.address3 != '') {
			this.httpService.queryParentZoneAll({ zoneId: this.creditCustBean.address3 }).subscribe(data => {
				this.provinceOptionsBasic_3 = this.commfunc.listToLabelvalue(data.firstZone, 'zoneName', 'zoneId');
				this.provinceOptionsBasic_3.unshift({ label: '请选择', value: '' });
				this.cityOptionsBasic_3 = this.commfunc.listToLabelvalue(data.secondZone, 'zoneName', 'zoneId');
				this.cityOptionsBasic_3.unshift({ label: '请选择', value: '' });
				this.zoneOptionsBasic_3 = this.commfunc.listToLabelvalue(data.thirdZone, 'zoneName', 'zoneId');
				this.zoneOptionsBasic_3.unshift({ label: '请选择', value: '' });
				this.streetOptionsBasic_3 = this.commfunc.listToLabelvalue(data.fourthZone, 'zoneName', 'zoneId');
				this.streetOptionsBasic_3.unshift({ label: '请选择', value: '' });
				data.zoneList.forEach(item => {
					switch (item.parentZoneLevel) {
						case '1': this.province_3 = item.parentZoneId; break;
						case '2': this.city_3 = item.parentZoneId; break;
						case '3': this.zone_3 = item.parentZoneId; break;
						case '4': this.street_3 = item.parentZoneId; break;
						default: break;
					}
				})
			})
		} else {
			let a = '';
			this.queryZoneInfo_3(a);//经营地址：
		}
	}
