	//根据客户经理号，查客户经理名称
	queryMange(item) {
		if (item.openOwnerId) {
			let param = {
				tellerId: item.openOwnerId
			}
			this.uisftechCommonHttpService.queryTellerByCombConditions(param).subscribe(data => {
				if (data.returnCode.code == 'success') {
					data.tellerList.forEach(element => {
						if (element.tellerId == item.openOwnerId) {
							this.tellerName = element.tellerName;
							this.creditCustBean.openOwnerId = element.tellerId;
						}
					});

				}
			});

		}
	}
	//根据客户经理所在机构，查机构名称
	queryOrg(item) {
		if (item.openOwnerOrgId) {
			let param = {
				orgId: item.openOwnerOrgId,
				flag: '0'
			}
			this.commonHttpService.queryOrgByCombConditions(param).subscribe(data => {
				if (data.returnCode.code == 'success') {
					this.orgName = data.orgInfoList[0].orgName.replace('台州银行', '');
					this.creditCustBean.openOwnerOrgId = data.orgInfoList[0].orgId;
				}
			});
		}
	}
