import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

import { Message, ConfirmationService } from 'primeng/primeng';

import { PageBean } from '../../common-view/bean/page.bean';
import { ViewRelationBean } from '../bean/viewrelation.bean';
import { CommonHttpService } from 'app/pages/tzb/common/http/common.http.service';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { SelectPostBean } from '../../../../marketing/marketing-activite/marketing-channel/select-post/bean/select-post.bean';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';

@Component({
  selector: 'app-common-viewrelation-add',
  templateUrl: './common-viewrelation-add.component.html',
  styleUrls: ['./common-viewrelation-add.component.scss'],
})
export class CommonViewrelationAddComponent implements OnInit {
  businessTypeList = [] //业务类型数据字典
  pageBean = new PageBean();
  rolePageBean = new PageBean();
  viewRelationBean = new ViewRelationBean();
  msgs: Message[] = [];
  SaveSuccessMsgs: Message[] = [];
  showView = false;
  showRoleList = false;
  sendViewParmas: any;
  showProductList: boolean;//显示产品列表
  productList: any[];//产品列表
  s_productId: string;
  s_productName: string;
  isCatelog: boolean = true;
  selectProductDisplay: boolean = false;//弹出框
  selectPostBean: SelectPostBean = new SelectPostBean();
  first: number = 0;//分页控制

  myformChild: FormGroup = this.fb.group({
    'businessType': new FormControl(''),
    'productNo': new FormControl(''),
    'viewName': new FormControl('', [Validators.pattern(/^[a-zA-Z0-9\u4e00-\u9fa5]{1,30}$/), Validators.required]),
  }); //校验
  constructor(
    private router: Router,
    public fb: FormBuilder,
    private location: Location,
    public commfunc: Commfunc,
    public commonHttpService: CommonHttpService,
    private creditCcmService: CreditCcmService,
    private confirmationService: ConfirmationService,
    public cusOperationHttpService: CusOperationHttpService
  ) { }
  ngOnInit() {
    this.getCodeValues();
  }
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.businessTypeList = codeValues['MarketingCampaigntType'];
  }
  importView() {
    this.showView = true;
    this.sendViewParmas = {
      'flag': 'viewrelationAdd'
    };
  }
  // 重置
  resetBtn() {
    this.viewRelationBean = new ViewRelationBean();
  }
  backBtn() {
    this.location.back();
  }
  // 引入角色
  addRoles() {
    this.showRoleList = true;
    this.queryRoleList('initQuery')
  }

  queryRoleList(param?: string) {
    this.rolePageBean.selectedList = [];
    if (param === 'initQuery') {
      this.rolePageBean.pageSize = 5;
      this.rolePageBean.currentPage = 1;
    };
    const queryParams = {
      'pageSize': this.rolePageBean.pageSize,
      'pageNum': this.rolePageBean.currentPage,
      'postId': this.rolePageBean.postId,
      'postName': this.rolePageBean.postName
    }
    this.commonHttpService.queryPostById(queryParams).subscribe(data => {
      if (data.returnCode.code === 'success') {
        this.rolePageBean.queryList = data.postInfoList;
        this.rolePageBean.totalRecords = data.total;
      } else {
        this.msgs = [{ severity: 'error', summary: '错误:', detail: data.returnCode.message }];
      }

    })
  }
  // 角色分页按钮事件
  rolePaginate(event) {
    this.rolePageBean.currentPage = event.page + 1;
    this.rolePageBean.pageSize = event.rows;
    this.rolePageBean.startIndex = event.first;
    this.queryRoleList();
  }
  // 确定-把弹出框的数据传到父页面的数据列表中
  confirmBtn() {
    if (this.pageBean.queryList && this.pageBean.queryList.length === 0) {
      this.pageBean.queryList = this.rolePageBean.selectedList;
    } else if (this.pageBean.queryList && this.pageBean.queryList.length > 0) {
      for (const item of this.rolePageBean.selectedList) {
        if (this.pageBean.queryList && this.pageBean.queryList.findIndex((temp) => temp['postId'] === item['postId']) === -1) {
          this.pageBean.queryList.push(item);
        }
      }
    }
    this.showRoleList = false;
  }
  //删除
  delete(index) {
    this.pageBean.queryList.splice(index, 1);
  }
  // 提交之前做一次校验
  preValidateForSubmit() {
    //校验的
    for (const i in this.myformChild.controls) this.myformChild.controls[i].markAsDirty();
    //处理是否提交
    for (const i in this.myformChild.controls) {
      if (!this.myformChild.controls[i].valid) return false;
    }
    return true;
  }
  saveBtn() {
    // this.queryProductName()
    
    //console.log(this.pageBean.queryList)
    if (!this.preValidateForSubmit()) return;

    if (this.pageBean.queryList.length < 1) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请至少勾选一条角色数据！' }];
      return;
    }
    const roleList = [];
    for (const role of this.pageBean.queryList) {
      let obj = {};
      obj['roleId'] = role['postId'];
      obj['roleName'] = role['postName'];
      roleList.push(obj);
    }
    const params = {
      'businessType': this.viewRelationBean.businessType,
      'productNo': this.viewRelationBean.productNo,
      'productName': this.viewRelationBean.productName,
      'viewConfigId': this.viewRelationBean.viewConfigId,
      'viewName': this.viewRelationBean.viewName,
      'roleList': roleList
    };
    this.creditCcmService.addViewRelations(params).subscribe(data => {
      if (data.returnCode.code === 'success') {
        this.SaveSuccessMsgs = [{ severity: 'success', summary: '提示', detail: '保存成功' }];
      } else {
        this.msgs = [{ severity: 'error', summary: '失败', detail: data.returnCode.message }];
      }
    });
  }

  addViewCallback(event) {
    this.showView = false;
    this.viewRelationBean.viewConfigId = event[0]['viewConfigId'];
    this.viewRelationBean.viewName = event[0]['viewName'];
  }

  //产品查询
  queryProListClick() {
    // let params={
    //   productId:this.s_productId,
    //   productName:this.s_productName
    // }
    // this.creditCcmService.addViewRelations(params).subscribe(data => {
    //   if (data.returnCode.code === 'success') {
    //     this.productList=data
    //   } else {
    //     this.msgs = [{ severity: 'error', summary: '失败', detail: data.returnCode.message }];
    //   }
    // });
  }
  // 选择产品
  choosePro() {
    this.selectProductDisplay = true;
  }
  productCall(event) {
    this.selectProductDisplay = false;
    this.viewRelationBean.productName = event[0].productName;
    this.viewRelationBean.productNo = event[0].productId;
  }
  //岗位弹出框查询
  query() {
    this.rolePageBean.pageSize = 5;
    this.rolePageBean.currentPage = 1;
    this.first = 0;
    this.queryRoleList();

  }
}

