    //     })
    //     if(!this.maxstatus){
    //         this.isZge = true;
    //       this.confirmationService.confirm({
    //          message: '最高额担保合同状态异常' + this.tipMessages,
    //          accept: () => {
    //           this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/approve-page']);
    //          }
    //      });
    //     }
    //     if(!this.maxstatus){
    //         return;
    //     }
    this.applyAmt();
