  checkMaxContractStatus(resolve) {
    // console.log("ad")
    const params = {
      custRequestNo: this.data.transferCreditData['custRequestNo'],
      processId: this.data.transferCreditData['processId']
    }
    return this.creditCcmService.checkMaxContractStatus(params).subscribe(data => {
      if (data.returnCode.code === 'success') {
        if (data.maxContractStatus == 'S') {
          resolve({
            maxStatus: true
          })
          //  return true;
        } else {
          this.tipMessages = data.tipMessages;
          resolve({
            maxStatus: false
          })
          // return false;
        }
      } else {
        this.tipMessages = data.returnCode.message
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
        resolve({
          maxStatus: false
        })
        // return false;
      }
    });
  }

  //还款计划表查询
  queryRepaymentPlan() {
    const params = {
      custRequestNo: this.data.transferCreditData['custRequestNo'],
      // custRequestNo:'99000950198',
      custRequestId: 'commit'
    };
    this.creditCcmService.repaymentPlanInit(params).subscribe(data => {
      if (data.returnCode.code === 'success') {
        if (data.baseMessage.repMessage) {
          if (data.baseMessage.repMessage == 'yes') {
            this.submitFun()
          } else {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '还款计划表未保存，不允许提交' }];
            return;
          }
        } else {
          this.msgs = [{ severity: 'error', summary: '提示', detail: '还款计划表查询异常' }];
        }
      } else {
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    });
  }
