  //   if (this.custInfoList.length == 0) {
  //     this.messg = [{ severity: 'info', summary: '提示', detail: '请刷新后确认担保或共同借款人信息后再提交' }];
  //     // return
  //   }
  //   let userName = JSON.parse(this.commfunc.getSessionDataCH('chName')).userName;
  //   let param = {
  //     custInfoList: this.custInfoList,
  //     userName,
  //     isAsyncFlag: '2'
  //   }
  //   this.cusViewHttpService.checkCreditReportInfo(param).subscribe(data => {
  //     if (data.returnCode.code == 'success') {
