  submitFun() {
    if (this.changeNext != 'next') {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '请点击下一步进入授信信息页面保存信息后再提交' }]
      return
    }
    // if ((this.isTransformation && this.index == 1) || (!this.isTransformation && this.index == 0) || (!this.isTransformation && !this.index)) {
    //   this.communicationService.saveCreditInfo.emit(true);
    // }
    // if (!(this.commfunc.getSessionDataCH('saveMSGIdentyfy') && this.commfunc.getSessionDataCH('saveMSGIdentyfy') == 'true')) {
    //   this.msgs = [{ severity: 'error', summary: '提示', detail: '请保存信息后再提交' }]
    //   return
    // }
    //   if (this.commfunc.getSessionDataCH('applyAmts')) {
    //     this.applyAmts = JSON.parse(this.commfunc.getSessionDataCH('applyAmts'));
    // }
    this.submitFlag = true;
    this.maskLayer = true;
    this.queryGuarantorList();
    let applyAmt = new Promise((resolve, reject) => {
      // this.applyAmt(resolve);
      resolve({ applyAmt: this.applyAmts })
    })

    let custInfo = new Promise((resolve, reject) => {
      this.custInfo(resolve);
    })

    let queryDynamic = new Promise((resolve, reject) => {
      this.queryDynamicProductAttributeOrValue(resolve);
    })

    let typeValue = new Promise((resolve, reject) => {
      this.getBusinessHappenTypeValue(resolve);//判断是否是转化贷款
    })

    // let toBI = new Promise((resolve, reject) => {
    //   //如果有担保信息就调用BI报告的方法
    //   if (this.data.transferCreditData.varMap.taskType != '01') {
    //     this.toBI(resolve)
    //   } else {
    //     resolve()
    //   }
    // })
    let select = Promise.all([applyAmt, custInfo, queryDynamic, typeValue]);

    select.then((res) => {
      if (!this.applyAmts) {
        this.msgs = [{ severity: 'error', summary: '提示', detail: '申请金额尚未查出，请刷新后重新提交' }];
        return;
      }
      this.isCompleteCustInfo = res[1]['isCompleteCustInfo'];
      this.questionType = res[2]['questionType'];

      this.selectPerson(this.applyAmts, this.questionType, this.isCompleteCustInfo)
    })
  }

  //选人
  selectPerson(applyAmts, questionType, isCompleteCustInfo) {

    // this.submitFlag = true;
    // this.applyAmt()
    // this.custInfo();
    // this.queryDynamicProductAttributeOrValue()
    // setTimeout(() => {
    //判断金额是否变化
    let oldPmt = this.suggestLimit;
    let newPmt = this.applyAmts;
    let quotaFlag = '';
    if (parseInt(newPmt) <= parseInt(oldPmt)) {
      quotaFlag = '0';
    } else {
      quotaFlag = '1';
    }

    let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (s.extParam.specifyNextOpers) {
      delete s.extParam.specifyNextOpers
      let commonHeader = s;
      this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    }

    this.validateRules();//例外审批检查规则

    this.listDisplay = true;//
    let aka = this.commfunc.getSessionData('task');
    if (aka == 'task') {
      var l = JSON.parse(this.commfunc.getSessionDataCH('investiGatorName'));
      if (l) {
        this.validateRulesBean.investiGatorName2 = l["a"];
      }
    };
    this.validateRulesBean.quotaFlag = quotaFlag;
    this.validateRulesBean.businessType = this.data.transferCreditData['businessType'];
    this.validateRulesBean['PCA0000013'] = this.displayComponent;//是否为转化贷款
    this.waitSelUserList = this.commfunc.query1(this.data.transferCreditData['taskId'], this.validateRulesBean, this.data.transferCreditData.mainFlowNo);
    if (this.waitSelUserList.waitSelUserList.length > 0 && this.waitSelUserList.waitSelUserList[0].reminder) {
      this.commonMap['OUT_MSGList'] = JSON.parse(this.waitSelUserList.waitSelUserList[0].reminder)[0].toString();
      this.commonMap['OUT_exceptionalPosition'] = JSON.parse(this.waitSelUserList.waitSelUserList[0].reminder)[2];
      this.commonMap['OUT_finalApprovalAmount'] = JSON.parse(this.waitSelUserList.waitSelUserList[0].reminder)[1];
      this.commonMap['P_loanNum'] = JSON.parse(this.waitSelUserList.waitSelUserList[0].reminder)[3];
      this.commonMap['compreCreditFlag'] = JSON.parse(this.waitSelUserList.waitSelUserList[0].reminder)[4];
    }
    // 
    if (!this.waitSelUserList.erro) {
      this.outVal = this.waitSelUserList.waitSelUserList;
      let chooseThis = this.waitSelUserList.display;
      let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      let a2 = commonHeader.extParam.specifyNextOpers;
      if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
        this.downloadPerson = true;
      } else {

        // this.msgs = [];
        // this.msgs = [{ severity: 'error', summary: '提示', detail: '没有选人!' }];
        this.submitCreditApprove('1', applyAmts, questionType, isCompleteCustInfo);
        return;
        // this.comeBack();
      }

      //例外审批检查规则
      if (this.waitSelUserList.waitSelUserList && this.waitSelUserList.waitSelUserList.length != 0) {
        var reg = /[\u4e00-\u9fa5]/g;//校验是否是汉字
        if (!this.waitSelUserList.waitSelUserList[0].reminder || !reg.test(this.waitSelUserList.waitSelUserList[0].reminder)) {
          this.listDisplay = false;
        } else {
          this.list = [];
          let arrTemp = JSON.parse(this.waitSelUserList.waitSelUserList[0].reminder)[0];
          if (arrTemp && arrTemp.length != 0) {
            arrTemp.forEach(element => {
              let ind1, ind2;
              let arr2, arr3;
              let blackListId;
              if (element.indexOf('名单制') != -1 && element.indexOf('(') != -1) {
                ind1 = element.indexOf('(');
                ind2 = element.indexOf(')');
                blackListId = element.substring(ind1 + 1, ind2);
                if (blackListId == '${blackListId}' || blackListId == 'null') {
                  blackListId = null;
                }
                arr2 = element.split('');
                arr2.splice(ind1, ind2 - ind1 + 1);
                arr3 = arr2.join('');
              } else {
                arr3 = element;
              }
              let arr = { reminder: arr3, blackListId: blackListId };
              this.list.push(arr);
            });
          }
        }

      }
      if (this.isRealEstate) {//仅作提示
        this.listDisplay = true;
        let arr = { reminder: '借款企业为房地产企业，原则上不得发放流动性贷款；另需核实四证是否齐全和开发项目资本金比例是否到位，请确认合规性', linkCustNo: '' };
        if (!(this.list.length > 0 && this.list[this.list.length - 1].reminder.indexOf('借款企业为房地产企业') > -1)) {
          this.list.push(arr);
        }
        // this.messg = [{ severity: 'info', summary: '提示', detail: '借款企业为房地产企业，原则上不得发放流动性贷款；另需核实四证是否齐全和开发项目资本金比例是否到位，请确认合规性' }];
      }
      this.submitFlag = false;
      this.maskLayer = false
    } else {
      this.maskLayer = false;
      this.submitFlag = false;
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
    }
    // }, 300);

  }

