  //判断是否是主联系人，只有只有主联系人才有提交按钮
  selectPartyRelationByAdminDoor() {
    if (this.viewPageData && this.viewPageData['isPreview']) {//预览模式
      this.transferCreditBean.isPreview = true;
      return
    }
    this.creditCcmService.selectPartyRelationByAdminDoor({ custList: [{ custNo: this.data.transferCreditData['custNo'] }] }).subscribe(data => {
      if (data.returnCode.code == 'success') {
        data.custList[0].custInfoList.forEach(element => {
          for (let i of data.custList[0].custInfoList) {
            if (i.custManagerNos == '0301') {
              this.isKH = true;
              break;
            } else {
              this.isKH = false;
            }
          }
        });
      } else {
        this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
      }
    })
  }


