  // 获取明细信息批量预授信
  getDetailList(batchId) {
    let params = {
      batchId: batchId.batchId,
      pageNum: 1,
      pageSize: 10,
      operType: '1',
      operationType: '2',
      groupId: batchId.groupId

    }
    this.creditCcmService.queryBatchPreCreditDetailInfo(params).subscribe(data => {
      if (data.returnCode.code === 'success') {
        if (data.resultList.length >= 1) {
          this.submitBatchTwo()
        } else {
          this.msgs = [{ severity: 'error', summary: '错误', detail: "未查出客户明细" }];
        }

      } else {
        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
      }
    })
  }
  // 批量预授信
  submitBatchTwo() {
    let data = JSON.parse(this.commfunc.getSessionDataCH('createBatchBean'));
    let param = {};
    if (this.isDraftsman) {
      param = {
        operationType: "1",
        batchId: data.batchId,
        groupId: data.groupId,
        isBackFlag: '0',
        // isBackFlag:'0',
        taskId: this.draftsmanTaskId,
        isSubmit: '1'
      }
    } else {
      param = {
        operationType: "1",
        batchId: data.batchId,
        groupId: data.groupId,
        isSubmit: '1'
      }
    }
    // this.WorkType = JSON.parse(this.commfunc.getSessionDataCH('creditDataWorkFlow'));
    // var param;
    // if (this.WorkType && this.WorkType['workType'] == '4' || this.WorkType['workType'] == '5' || this.WorkType['workType'] == '7') {
    //   param = {
    //     operationType: "1",
    //     batchId: data.batchId,
    //     groupId: data.groupId,
    //     isBackFlag: '0',
    //     taskId: this.WorkType.taskId
    //   }
    // } else {
    //   param = {
    //     operationType: "1",
    //     batchId: data.batchId,
    //     groupId: data.groupId
    //   }
    // }
    this.creditCcmService.deleteBatchPreCredit(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.batchFlag = true;
        this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/approve-page']);
        //this.router.navigate(['/pages/tzb/custom/custom-group/group-batch-credit', { groupId: this.viewPageDatas.groupId, groupName: this.viewPageDatas.groupName, groupType: this.viewPageDatas.groupType }]);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: "提交申请成功" });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  // 打开名单制详情
  toCustListDetail(col) {
    if (col.reminder.indexOf('名单制') != -1) {
      const param = {
        blackListId: col.blackListId,
        registerUserName: JSON.parse(this.commfunc.getSessionDataCH('chName')).userName,
        operationType: '1007'
      }
      this.httpService.selectBlackListInfo(param).subscribe(data => {
        if (data.returnCode.code == "success") {
          let inValue = data.returnList;
          if (inValue.length > 0) {
            this.blackListInValue = inValue[0];
            this.blackListDisplay = true;
          } else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '查询客户名单制为空' }];
          }
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
      })
    }
  }
  // 关闭名单制详情
  closeBlackList() {
    this.blackListInValue = undefined;
    this.blackListDisplay = false;
  }
