import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

import { Message, ConfirmationService } from 'primeng/primeng';

import { ParamsBean } from '../bean/params.bean';
import { BookmarkBean } from '../bean/bookmark.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    selector: 'app-bookmark-add',
    templateUrl: './common-bookmark-add.component.html',
    styleUrls: ['./common-bookmark-add.component.scss'],
})
// tslint:disable-next-line:component-class-suffix
export class BookmarkAdd implements OnInit {
    // 提示信息
    msgs: Message[] = [];
    successMsgs: Message[] = [];
    // 校验
    myform: FormGroup;
    // 保存按钮控制
    showSaveBtn = true;
    // 页签关联资源页面展示
    displayConfirm = false;
    // 资源页面展示
    displayResource = false;
    // 页签bean
    bookmarkBean: BookmarkBean = new BookmarkBean();
    // 查询条件
    paramsBean: ParamsBean = new ParamsBean();
    // 资源回调
    param_resource: any = [];
    // 资源编号
    myformChild: FormGroup = this.fb.group({
        'bookmarkId': new FormControl(''),
        'bookmarkName': new FormControl('', [Validators.pattern(/^[a-zA-Z0-9\u4e00-\u9fa5]{1,30}$/), Validators.required]),

    }); //校验
    constructor(
        public fb: FormBuilder,
        private router: Router,
        public commfunc: Commfunc,
        private location: Location,
        private creditCcmService: CreditCcmService,
        private confirmationService: ConfirmationService,
    ) { }
    ngOnInit() {
        this.paramsBean.queryList = [];
        // 获取用户信息
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.bookmarkBean.tellerNo = commonHeader['userId'];
        this.bookmarkBean.tellerOrgNo = commonHeader['orgId'];
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.bookmarkBean.tellerName = chName['userName'];
        this.bookmarkBean.tellerOrgName = chName['orgName'];
    }
    // 提交之前做一次校验
    preValidateForSubmit() {
        for (const i in this.myformChild.controls) this.myformChild.controls[i].markAsDirty();
        for (const i in this.myformChild.controls) {
            if (!this.myformChild.controls[i].valid) {
                return false;
            }
        }
        return true;
    }

    // 页签保存
    addBookmark() {
        if (!this.preValidateForSubmit()) return;
        //
        // if (this.paramsBean.queryList.length === 0) {
        //     this.msgs = [{ severity: 'warn', summary: '提示：', detail: '请选择资源信息' }];
        //     return;
        // }
        //
        this.creditCcmService.createBookmarkConfig(this.bookmarkBean).subscribe(data => {
            if (data.flag === '1') {
                this.msgs = [{ severity: 'warn', summary: '提示：', detail: '页签名称已经存在' }];
                return;
            }
            if (data.returnCode.code === 'success') {
                this.msgs = [{ severity: 'success', summary: '提示：', detail: '页签保存成功' }];
                this.bookmarkBean.bookmarkId = data['bookmarkId'];
                this.showSaveBtn = false;
                this.displayConfirm = true;
                //
                //this.toConfirm();
                //
            } else {
                this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
            }
        });
    }
    // 删除资源
    delete() {
        if (!this.paramsBean.selectedList) {
            this.msgs = [{ severity: 'info', summary: '提示', detail: '请至少选择一条数据！' }];
            return;
        }
        this.confirmationService.confirm({
            message: '确定要删除这些数据吗？',
            accept: () => {
                const tempList = [];
                for (const role of this.paramsBean.queryList) {
                    if (this.paramsBean.selectedList && this.paramsBean.selectedList.findIndex((temp) => temp === role) === -1) {
                        tempList.push(role);
                    }
                }
                this.paramsBean.queryList = tempList;
            },
            reject: () => {
                this.msgs = [{ severity: 'info', summary: '删除', detail: '你选择了拒绝删除！' }];
            }
        });
    }
    // 引入资源页面展示
    addBookmarkResource() {
        this.param_resource = { 'bookmark': 'bookmark', 'bookmarkId': this.bookmarkBean.bookmarkId };
        this.displayResource = true;
    }

    // 上移
    moveUpIcon(item): void {
        const order = this.paramsBean.queryList.indexOf(item);
        if (order === 0) {
            this.msgs = [{ severity: 'error', summary: '错误', detail: '当前已是第一个！' }];
        } else {
            const temp = this.paramsBean.queryList[order - 1];
            this.paramsBean.queryList[order - 1] = this.paramsBean.queryList[order];
            this.paramsBean.queryList[order] = temp;
        }
    }
    // 下移
    moveDownIcon(item): void {
        const order = this.paramsBean.queryList.indexOf(item);
        if (order === this.paramsBean.queryList.length - 1) {
            this.msgs = [{ severity: 'error', summary: '错误', detail: '当前已是最后一个！' }];
        } else {
            const temp = this.paramsBean.queryList[order + 1];
            this.paramsBean.queryList[order + 1] = this.paramsBean.queryList[order];
            this.paramsBean.queryList[order] = temp;
        }
    }
    // 从资源获取值到页签列表页面
    queryResource(event) {
        if (this.paramsBean.queryList && this.paramsBean.queryList.length > 0) {
            const tempList = this.paramsBean.queryList;
            for (const item of event) {
                if (tempList && tempList.findIndex((temp) => temp['basicSourceId'] === item['basicSourceId']) === -1) {
                    tempList.push(item);
                }
            }
            this.paramsBean.queryList = tempList;
        } else {
            for (const params of event) {
                this.paramsBean.queryList.push(params);
            }
        }
        this.displayResource = false;
    }
    // 确定保存页签关联资源
    toConfirm() {
        if (this.paramsBean.queryList.length === 0) {
            this.msgs = [{ severity: 'warn', summary: '提示：', detail: '请选择资源信息' }];
            return;
        }
        const basicSourceIds = [];
        for (let i = 0; i < this.paramsBean.queryList.length; i++) {
            basicSourceIds.push({
                'bookmarkSeq': this.bookmarkBean.bookmarkSeq,
                'basicSourceId': this.paramsBean.queryList[i].basicSourceId,
                'sourceOrder': (i + 1),
                'basicSourceName': this.paramsBean.queryList[i].basicSourceName
            });
        }
        const params = {
            'bookmarkId': this.bookmarkBean.bookmarkId,
            'bookmarkName': this.bookmarkBean.bookmarkName,
            'tellerOrgNo': this.bookmarkBean.tellerOrgNo,  // 经办机构
            'tellerOrgName': this.bookmarkBean.tellerOrgName,  // 经办机构名称
            'tellerNo': this.bookmarkBean.tellerNo,  // 经办人编号
            'tellerName': this.bookmarkBean.tellerName, // 经办人名称
            'list': basicSourceIds
        };
        this.creditCcmService.addBatchBookmarkConfig(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.successMsgs = [{ severity: 'success', summary: '提示：', detail: '页签关联资源保存成功' }];
            } else {
                this.msgs = [{ severity: 'error', summary: '错误：', detail: data.returnCode.message }];
            }
        });
    }
    // 返回
    toBack() {
        this.location.back();
    }
}
