import { Component, OnInit } from '@angular/core';
import { CreditGuarantorQuoteBean } from '../bean/credit-guarantor-quote.bean';
import { ParamsBean } from '../../bean/params.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
@Component({
    selector: 'app-credit-guarantor-quote',
    templateUrl: './credit-guarantor-quote.component.html',
    styleUrls: ['./credit-guarantor-quote.component.scss'],
    providers: [CreditCcmService]
})
// 引用最高额度合同
// tslint:disable-next-line:component-class-suffix
export class CreditGuarantorQuote implements OnInit {
    // 最高额度合同bean信息
    creditGuarantorQuoteBean: CreditGuarantorQuoteBean = new CreditGuarantorQuoteBean();
    // 分页参数信息
    paramsBean: ParamsBean = new ParamsBean();
    // 构造器
    constructor (private creditCcmService: CreditCcmService) { }
    // 初始化信息
    ngOnInit() {
        this.queryEsbMaximumAgreementInfo();
    }
    // 最高额合同担保人信息查询
    queryEsbMaximumAgreementInfo() {
        const params = {
            CONTRACT_NO: '123455',
        };
        this.creditCcmService.queryEsbMaximumAgreementInfo(params).subscribe(data => {
            if (data.responseMessage === 'success') {
                this.paramsBean.queryList = data.list;
            }
        });
    }

    queryGuarantor() {
        const param = {
            'facProcessId': '100010',
            'questionType': 'guaranteeInfo'
        };
        const first1 = [];
        this.creditCcmService.queryFacProcessQuestions(param).subscribe(data => {
            if (data.Service.Header.RetStatus === 'S') {
                for (const params of data.Service.Body.list) {
                    let str1 = '';
                    let str2 = '{';
                    str2 += '"facProcessQuestionId":' + '"' + params.facProcessQuestionId + '"' + ',';
                    str1 = params.questionReslponse;
                    str1 = str1.substr(1, str1.length - 2);
                    str2 += str1;
                    str2 = str2 + '}';
                    first1.push(JSON.parse(str2));
                }
                this.paramsBean.queryList = first1;
            }
        });
    }
}
