import { Router } from '@angular/router';
import { Component, OnInit, Input, OnChanges, Output, EventEmitter } from '@angular/core';

import { Message, ConfirmationService } from 'primeng/primeng';

import { ParamsBean } from './bean/params.bean';
import { BookmarkBean } from './bean/bookmark.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
@Component({
    selector: 'app-common-bookmark',
    templateUrl: './common-bookmark.component.html',
    styleUrls: ['./common-bookmark.component.scss'],
})
// tslint:disable-next-line:component-class-suffix
export class CommonBookmark implements OnInit, OnChanges {
    @Input() view_data: any;
    @Output() closeDialog = new EventEmitter();
    // 提示信息
    msgs: Message[] = [];
    // 控制新增修改删除按钮显示
    displayButton = true;
    // 展示页面关联页签保存按钮
    displayView = false;
    displayDetailView = false;
    paramsBean: ParamsBean = new ParamsBean();
    detailData: any = [];
    bookmarkBean: BookmarkBean = new BookmarkBean();
    constructor(
        private router: Router,
        private creditCcmService: CreditCcmService,
        private confirmationService: ConfirmationService
    ) { }
    ngOnInit() {
        this.query('queryBtn');
    }
    ngOnChanges() {
        if (this.view_data.view === 'view') {
            this.displayButton = false;
            this.displayView = true;
        }
    }

    // 确定传值到页面管理
    toConfirm() {
        if (!this.paramsBean.selectedList) {
            this.closeDialog.emit('empty');
        } else {
            this.closeDialog.emit(this.paramsBean.selectedList);
        }
    }
    query(param?: string) {
        if (param === 'queryBtn') {
            this.paramsBean.startIndex = 0;
            this.paramsBean.pageNum = 1;
        }
        const qureyBean = {
            'bookmarkId': this.bookmarkBean.bookmarkId,
            'bookmarkName': this.bookmarkBean.bookmarkName,
            'viewSize': this.paramsBean.pageSize,
            'viewIndex': this.paramsBean.pageNum
        };
        this.creditCcmService.queryBookMarkListByLike(qureyBean).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.paramsBean.queryList = data.list;
                this.paramsBean.totalRecords = data.totalSize;
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        });
    }
    // 分页按钮事件
    paginate(event) {
        this.paramsBean.pageNum = event.page + 1;
        this.paramsBean.pageSize = event.rows;
        this.paramsBean.startIndex = event.first;
        this.query();
    }
    // 资源删除
    delete() {
        if (!this.paramsBean.selectedList) {
            this.msgs = [{ severity: 'info', summary: '提示!', detail: '至少选择一条数据!' }];
            return;
        }
        this.confirmationService.confirm({
            message: '确定要删除这些数据吗？',
            accept: () => {
                let temp = '';
                this.paramsBean.selectedList.forEach(element => {
                    temp += element.bookmarkId + ',';
                });
                temp = temp.substr(0, temp.length - 1);
                const bookmarkSeqJson = {
                    bookmarkId: temp
                };
                // 调用组件删除接口
                this.creditCcmService.deleteBookmark(bookmarkSeqJson).subscribe(data => {
                    if (data.returnCode.code === 'success') {
                        this.query();
                    } else {
                        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
                    }
                });
            },
            reject: () => {
                this.msgs = [{ severity: 'info', summary: '删除', detail: '你选择了拒绝删除！' }];
            }
        });
    }
    // 预览
    preview() {
        if (this.paramsBean.selectedList && this.paramsBean.selectedList.length === 1) {
            this.detailData = this.paramsBean.selectedList[0];
            this.displayDetailView = true;
        } else if (!this.paramsBean.selectedList) {
            this.msgs = [{ severity: 'info', summary: '提示:', detail: '请选择一条数据!' }];
        } else if (this.paramsBean.selectedList && this.paramsBean.selectedList.length > 1) {
            this.msgs = [{ severity: 'error', summary: '错误：', detail: '只能选择一条数据!' }];
        }
    }
    // 跳转新增页面
    add() {
        this.router.navigate(['/pages/tzb/custom/credit-ccm/bookmarkAdd']);
    }
    edit() {
        if (this.paramsBean.selectedList && this.paramsBean.selectedList.length === 1) {
            this.router.navigate(['/pages/tzb/custom/credit-ccm/bookmarkUpdate', JSON.stringify(this.paramsBean.selectedList[0])]);
        } else if (!this.paramsBean.selectedList) {
            this.msgs = [{ severity: 'info', summary: '提示:', detail: '请选择一条数据!' }];
        } else if (this.paramsBean.selectedList && this.paramsBean.selectedList.length > 1) {
            this.msgs = [{ severity: 'error', summary: '错误：', detail: '只能选择一条数据!' }];
        }
    }
    reset() {
        this.paramsBean = new ParamsBean();
        this.bookmarkBean = new BookmarkBean();
    }
}
