import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'CreguaranteeMoneyPie',
    pure: true
})

export class CreguaranteeMoneyPie implements PipeTransform {
    name: string;
    constructor(
    ) {

    }
    transform(s: any): any {
         /**
         * 参数说明
         * s:要格式化的数字
         * n:保留几位小数
         * 
         */
        // n = n > 0 && n <= 20 ? n : 2;
        // s = parseFloat((s + "").replace(/[^\d\.-]/g,"")).toFixed(2) + "";
        // let l = s.split(".")[0].split("").reverse();
        // let r = s.split(".")[1];
        // let t = "";
        // for(let i = 0; i < l.length; i++){
        //     t += l[i] + ((i+1)%3 ==0 && (i+1) != l.length ? "," : "");
        // }
        // return t.split("").reverse().join("") + "." + r;

        if(s !== undefined&&s !== ''){
            let str = (s/1).toFixed(2) + '';
            let intSum = str.substring(0,str.indexOf(".")).replace(/\B(?=(?:\d{3})+$)/g,',');//取整数部分
            let dot = str.substring(str.length,str.indexOf("."));//取小数部分
            let ret = intSum + dot;
            return ret;
        }else{
            return s;
        }
    }
}